<template>
    <div class="info-content">
        <table>
            <tr class="column-row" v-if="showFromAccount">
                <td class="column-label">{{$store.getters.getAffiliationTranslation('From_Account_vod__c')}}</td>
                <td class="column-value">{{aff.FromAcct.Formatted_Name_vod__c}}</td>
            </tr>
            <tr class="column-row" v-if="showToAccount">
                <td class="column-label">{{$store.getters.getAffiliationTranslation('To_Account_vod__c')}}</td>
                <td class="column-value">{{aff.ToAcct.Formatted_Name_vod__c}}</td>
            </tr>
            <tr class="column-row" v-if="showStrength">
                <td class="column-label">{{$store.getters.getAffiliationTranslation('Relationship_Strength_vod__c')}}</td>
                <td class="column-value">{{$store.getters.getPicklistTranslation('Relationship_Strength_vod__c', aff.Strength)}}</td>
            </tr>
            <tr class="column-row" v-if="showInfluence">
                <td class="column-label">{{$store.getters.getAffiliationTranslation('Influence_vod__c')}}</td>
                <td class="column-value">{{$store.getters.getPicklistTranslation('Influence_vod__c', aff.Influence)}}</td>
            </tr>
            <tr class="column-row" v-if="showTherapeuticArea">
                <td class="column-label">{{$store.getters.getAffiliationTranslation('Therapeutic_Area_vod__c')}}</td>
                <td class="column-value">{{therapeuticArea}}</td>
            </tr>
            <tr class="column-row" v-if="showRole">
                <td class="column-label">{{$store.getters.getAffiliationTranslation('Role_vod__c')}}</td>
                <td class="column-value">{{$store.getters.getPicklistTranslation('Role_vod__c', aff.Role)}}</td>
            </tr>
            <tr class="column-row" v-if="showComments">
                <td class="column-label">{{$store.getters.getAffiliationTranslation('Comments_vod__c')}}</td>
                <td class="column-value">{{aff.Comments}}</td>
            </tr>
        </table>
    </div>
</template>

<script>
export default {
    name: 'AffiliationInfo',
    props: {
        aff: {
            type: Object
        }
    },
    computed: {
        showToAccount: function() {
            return this.aff && this.aff.ToAcct && this.aff.ToAcct.Formatted_Name_vod__c;
        },
        showFromAccount: function() {
            return this.aff && this.aff.FromAcct && this.aff.FromAcct.Formatted_Name_vod__c;
        },
        showStrength: function() {
            return this.aff && this.aff.Strength;
        },
        showInfluence: function() {
            return this.aff && this.aff.Influence;
        },
        showTherapeuticArea: function() {
            return this.aff && this.aff.TherapeuticArea && this.aff.TherapeuticArea.length > 0;
        },
        therapeuticArea: function() {
            return this.aff && this.aff.TherapeuticArea ? this.getTranslatedValues(this.aff.TherapeuticArea, "Therapeutic_Area_vod__c").join(", ") : [];
        },
        showRole: function() {
            return this.aff && this.aff.Role;
        },
        showComments: function() {
            return this.aff && this.aff.Comments;
        }
    },
    methods: {
        getTranslatedValues(values, fieldName) {
            let translatedValues = [];
            values.forEach(value => {
                translatedValues.push(this.$store.getters.getPicklistTranslation(fieldName, value));
            })
            return translatedValues;
        }
    }
}
</script>

