<template>
    <div class="search-detail">
        <div class="search-box-container">
            <input class="search-box" v-model="inputText" :placeholder="$store.getters.FIND_BUTTON">
            <button class="search-button" @click="getSearchResults"><img src="icons/search.png" /></button>
        </div>
        <div v-if="showResults" class="search-results">
            <hr class="search-results-divider">
            <ul>
                <li v-for="result in results" :key="result.Id">
                    <child-tile :account="result" 
                                @toggle-accounts="$emit('toggle-accounts', $event)"
                                @ping-account="$emit('ping-account', $event)"/>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import ChildTile from './ChildTile.vue'

export default {
    name: 'Search',
    components: {
        ChildTile
    },
    data() {
        return {
            inputText: "",
            showResults: false
        }
    },
    props: {
        results: {
            type: Array
        }
    },
    watch: {
        results: {
            immediate: true,
            deep: true,
            handler(val) {
                this.showResults = val.length > 0; 
            }
        }
    },
    methods: {
        getSearchResults() {
            this.$emit("search", this.inputText);
        }
    }
}
</script>

