<template>
	<div class="root">
		<div id="tree"></div>
		<graph-menu
			:searchResult="searchResultCopy"
			:filterValues="filterValues"
			@fit="fit"
			@zoom-in="zoomIn"
			@zoom-out="zoomOut"
			@toggle-accounts="toggleAccountAndParentVisibility($event)"
			@ping-account="pingAccount($event)"
			@search="populateSearchResult($event)"
			@clear-search="clearSearchResults"
			@apply-filter="applyFilter($event)"
		/>
		<loading-modal v-show="$store.getters.graphIsLoading"/>
		<error-modal v-if="$store.getters.getGraphErrorState"/>
		<account-detail
			v-show="showAccountDetail"
			:account="selectedAccount"
			@close-detail="closeAccountDetail"
			@toggle-accounts="toggleAccountAndParentVisibility($event)"
			@ping-account="pingAccount($event)"
		/>
	</div>
</template>

<script>
import LoadingModal from "./components/LoadingModal.vue";
import ErrorModal from "./components/ErrorModal.vue";
import AccountDetail from "./components/AccountDetail.vue";
import GraphMenu from "./components/GraphMenu.vue";
import GraphController from "@/core/graphController.js";
import MessagingService from "@/core/messagingService.js";
import MockDataService from "@/core/util/MockDataService.js";
import { saveAs } from "@/core/util/FileSaver.js";

let controller = new GraphController();
let ms = new MessagingService(false);
let isPostMessageReady = false;
let isFirstLoad = true;
//ENSURE THIS IS SET TO FALSE BEFORE COMMITTING
let isDebugMode = false;

export default {
	name: "App",
	components: {
		LoadingModal,
		ErrorModal,
		AccountDetail,
		GraphMenu
	},
	data() {
		return {
			searchResult: [],
			searchResultCopy: [],
			showAccountDetail: false,
			selectedAccount: {},
			filterValues: {}
		};
	},
	mounted() {
		ms.addMessageHandler(this.handleMessage);
		this.$store.dispatch("showLoadingModal");
		this.sendInitialLoadRequest();
	},
	methods: {
		handleMessage(event) {
			try {
				switch(event.command) {
					case "ping":
						isPostMessageReady = true;
						break;
				
					case "initGraph":
						this.initGraph(event.data);
						break;

					case "loadInitialHierarchy":
						this.loadHierarchy(event.data, true, MockDataService.getLoadInitialBatch)
						break;

					case "loadHierarchy":
						this.loadHierarchy(event.data, false, MockDataService.getLoadDownBatch)
						break;

					case "loadParentHierarchy":
						this.loadHierarchy(event.data, false, MockDataService.getLoadUpBatch);
						break;

					case "loadAccountBatch": {
						this.loadAccountBatch(event.data);
						break;
					}
					case "toggleAffiliations":
						controller.toggleAffiliations();
						this.$store.dispatch("hideLoadingModal");
						break;

					case "error":
						this.displayError(this.$store.getters.LOADING_ERROR_GENERIC, event.data, true);
						break;

					case "exportPNG":
						this.exportPng();
						break;
				}				
			} catch (e) {
				console.log("EXCEPTION!");
				console.log(e);
				this.displayError(this.$store.getters.LOADING_ERROR_GENERIC, e, true);
			}
		},

		sendInitialLoadRequest() {
			//it is indeterminate which post message handler will be set up first, so pinging before
			//sending the initial request ensures the lightning controller side is ready to receive messages
			if (!isDebugMode) {
				if (isPostMessageReady) {
					ms.sendInitialLoadRequest();
				} else {
					setTimeout(() => {
						this.sendInitialLoadRequest();
					}, 100);
					ms.sendPing();
				}
			} else {
				this.handleMessage(MockDataService.getInitializeData());
			}
		},

		initGraph(data) {
			this.$store.commit("setVeevaMessages", data.veevaMessages);
			this.$store.commit("setObjectTranslations", data.labelTranslations);
			this.$store.commit("setPicklistTranslations", data.picklistTranslations);
			controller.init(
				this.accountTimelineClick,
				this.accountDetailClick,
				this.redrawHandler,
				this.nodeClick,
				this.$store.getters.NAVIGATE_TO_ACCOUNT,
				this.$store.getters.NAVIGATE_TO_TIMELINE,
				data.keyStakeholders,
				data.graphRootId
			);
			if (isDebugMode) {
				this.handleMessage(MockDataService.getLoadInitialHier());
			} else {
				ms.sendInitialHierarchyRequest(data.graphRootId);
			}
		},

		loadHierarchy(data, isInitial, mockDataFunc) {
			let batchesToProcess = controller.loadHierarchy(data, isInitial);
			if (!batchesToProcess || batchesToProcess.length === 0) {
				//if nothing to process, hide loading modal
				this.$store.dispatch("hideLoadingModal");
				controller.updateNodes();
				return;
			}
			if (data.queryLimitTriggered) {
				this.displayError(this.$store.getters.LIMIT_EXCEEDED, "", false);
			}
			if (!isDebugMode) {
				this.processBatches(batchesToProcess);
			} else {
				this.handleMessage(mockDataFunc.call());
			}
		},

		loadAccountBatch(data) {
			if (data.limitTriggered) {
				this.displayError(this.$store.getters.LIMIT_EXCEEDED, "", false);
			}
			let isComplete = controller.processLoadBatch(data);
			if (isComplete) {
				this.filterValues = controller.getFilterValues();
				this.$store.dispatch("hideLoadingModal");
				this.forceUpdateObjects();
				ms.sendAccountCount(controller.getAccountCount());
				if (isFirstLoad) {
					controller.fit();
					isFirstLoad = false;
				}
			}
		},

		processBatches(batchesToProcess) {
			batchesToProcess.forEach(batch => {
				ms.sendAccountLoadRequest(batch.batchId, batch.accounts);
			});
		},

		exportPng() {
			window.domtoimage
				.toBlob(document.getElementById("tree"))
				.then(function(blob) {
					saveAs(blob, "test.png");
				});
			this.$store.dispatch("hideLoadingModal");
		},

		accountTimelineClick(nodeId) {
			controller.accountTimelineClick(nodeId);
		},

		accountDetailClick(nodeId) {
			controller.accountDetailClick(nodeId);
		},

		nodeClick(sender, args) {
			let nodeClickAction = controller.nodeClickHandler(sender, args);
			if (nodeClickAction === "loadParent") {
				let nodeId = args.node.id;
				if (!isDebugMode) {
					this.$store.dispatch("showLoadingModal");
					ms.sendParentHierarchyRequest(nodeId.split('-')[1]);
				} else {
					this.handleMessage(MockDataService.getLoadUpHier());
				}
			} else if (nodeClickAction === "detailView") {
				let clickedNodeId = args.node.id;
				controller.selectAccount(clickedNodeId);
				this.populateAccountDetail(clickedNodeId);
				this.showAccountDetail = true;
			} else {
				//load more siblings action
				this.forceUpdateObjects();
			}
			return false;
		},

		populateAccountDetail(id) {
			let nodeDataModel = controller.getNodeDataModel(id);
			this.selectedAccount = nodeDataModel;
		},

		redrawHandler() {
			let self = this;
			let loadChildrenBtns = document.querySelectorAll(".load-children-button");
			for (let loadElement of loadChildrenBtns) {
				let hasClickHandler = loadElement.getAttribute("hasHandler") === "true";
				if (!hasClickHandler) {
					loadElement.setAttribute("hasHandler", "true");
					loadElement.addEventListener("click", function(e) {
							e.preventDefault();
							e.stopPropagation();
							let id = this.parentNode.getAttribute("node-id");
							if (!isDebugMode) {
								self.$store.dispatch("showLoadingModal");
								ms.sendHierarchyRequest(controller.getNodesToLoad(id));
							} else {
								self.handleMessage(MockDataService.getLoadDownHier());
							}
						},
						false
					);
				}
			}
			let expandCollapseBtns = document.querySelectorAll(".expand-collapse-button");
			for (let expElement of expandCollapseBtns) {
				let expHasClickHandler = expElement.getAttribute("hasHandler") === "true";
				if (!expHasClickHandler) {
					expElement.setAttribute("hasHandler", "true");
					expElement.addEventListener("click", function(e) {
							e.preventDefault();
							e.stopPropagation();
							let id = this.parentNode.getAttribute("node-id");
							controller.expandCollapseNodes(id);
							self.forceUpdateObjects();
						},
						false
					);
				}
			}
		},

		closeAccountDetail() {
			this.showAccountDetail = false;
			controller.deselectAccount();
		},

		toggleAccountAndParentVisibility(id) {
			controller.toggleAccountVisibility(id);
			this.forceUpdateObjects();
		},

		fit() {
			controller.fit();
		},

		zoomIn() {
			controller.zoomIn();
		},

		zoomOut() {
			controller.zoomOut();
		},

		pingAccount(id) {
			controller.pingAccount(id);
			this.forceUpdateObjects();
		},

		populateSearchResult(searchString) {
			this.searchResult = controller.getSearchResult(searchString);
			this.searchResultCopy = this.copyArray(this.searchResult);
		},

		applyFilter(filterObj) {
			this.$store.dispatch("showLoadingModal");
			setTimeout(() => {
				controller.processFilterRequest(filterObj)
				this.forceUpdateObjects();
				this.$store.dispatch("hideLoadingModal");
			}, 0);
		},

		forceUpdateObjects() {
			this.selectedAccount.isChanged = this.selectedAccount.isChanged ? false : true;
			if (this.searchResult.length > 0) {
				this.searchResultCopy = this.copyArray(this.searchResult);
			}
		},

		copyArray(arr) {
			let newArr = [];
			arr.forEach(a => {
				newArr.push(Object.assign({}, a));
			});
			return newArr;
		},
		
		clearSearchResults() {
			this.searchResult = [];
			this.searchResultCopy = [];
		},

		displayError(errorMessage, errorDetailMessage, hideLoading) {
			this.$store.commit("setGraphErrorMessage", errorMessage)
			this.$store.commit("setGraphErrorDetail", errorDetailMessage);
			this.$store.dispatch("openErrorModal");
			if (hideLoading) {
				this.$store.dispatch("hideLoadingModal");
			}
		}
	}
};
</script>

<style>
	#app {
		width: 100%;
		height: 100%;
	}

	.root {
		width: 100%;
		height: calc(100% - 62px);
	}

	.one-line {
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		word-break: break-all;
		-webkit-line-clamp: 1;
		-webkit-box-orient: vertical;
	}

	.two-line {
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
	}

	/* custom scrollbar */
	::-webkit-scrollbar {
		width: 13px;
	}

	::-webkit-scrollbar-track {
		background-color: transparent;
	}

	::-webkit-scrollbar-thumb {
		background-color: rgba(0, 0, 0, 0.1);
		border-radius: 100px;
		border-right: 5px solid transparent;
		border-top: 5px solid transparent;
		border-bottom: 5px solid transparent;
		background-clip: content-box;
	}

	::-webkit-scrollbar-thumb:hover {
		background-color: rgba(0, 0, 0, 0.2);
	}
</style>