import { createStore } from 'vuex'

export default createStore({
    state: {
        ui: {
            loading: true,
            error: false,
            errorDetail: "",
            errorMessage: ""
        },
        labels: {
            messages: {
                ACCOUNT_GROUPING_FILTERS: "Account Filters",
                ACCOUNT_GROUPING_LEGEND: "Account",
                AFFILIATIONS_ICON_LEGEND: "Number of Affiliations",
                AFFILIATION_GROUPING_FILTERS: "Affiliation Filters",
                AFFILIATION_GROUPING_LEGEND: "Affiliation",
                AFFILIATION_STRENGTH_NONE_LEGEND: "Strength: None",
                AFFILIATION_STRENGTH_0_LEGEND: "Strength: {0}",
                AFFILIATION_STRENGTH_1_LEGEND: "Strength: {0}",
                AFFILIATION_STRENGTH_2_LEGEND: "Strength: {0}",
                ALWAYS_SHOW_KEY_STAKEHOLDERS: "Always Show Key Stakeholders",
                CHILDREN_ICON_LEGEND: "Number of Children",
                FILTER_APPLY_BUTTON: "Apply",
                FILTER_CLEAR_BUTTON: "Clear",
                FIND_BUTTON: "Find...",
                HAS_INFLUENCE_LEGEND: "Is/Has Influence",
                NO_INFLUENCE_LEGEND: "No Influence",
                KEY_STAKEHOLDER_LEGEND: "Key Stakeholder",
                NAVIGATE_TO_ACCOUNT: "Account Detail",
                NAVIGATE_TO_TIMELINE: "Account Timeline",
                ONLY_SHOW_ACCOUNTS_WITH_AFFILIATIONS: "Only Show Accounts with Affiliations",
                ONLY_SHOW_AFFILIATIONS_WITH_INFLUENCE: "Only Show Affiliations with Influence",
                ROOT_ACCOUNT_LEGEND: "Root Account for Graph",
                SPECIALTIES_FILTER: "Specialties",
                STAKEHOLDER_NAVIGATOR_PAGE_NAME: "Stakeholder Navigator",
                TERRITORIES_FILTER: "Territories",
                TOGGLE_AFFILIATIONS: "Toggle Affiliations",
                UNFILTERED_PARENT_LEGEND: "Unfiltered Parent Account",
                ALL: "All",
                RECORD_TYPE_LABEL: "Record Type",
                LOADING_ERROR_GENERIC: "There was a problem loading the Stakeholder Navigator. Please contact your system administrator.",
                LIMIT_EXCEEDED: "Stakeholder Navigator could not load all accounts or relationships due to the number of accounts and relationships that exist for the parent account."
            },
            objectTranslations: {
                account: {},
                affiliation: {}
            },
            picklistTranslations: {}
        }
    },
    getters: {
        graphIsLoading: state => state.ui.loading,
        ACCOUNT_GROUPING_FILTERS: state => state.labels.messages.ACCOUNT_GROUPING_FILTERS,
        ACCOUNT_GROUPING_LEGEND: state => state.labels.messages.ACCOUNT_GROUPING_LEGEND,
        AFFILIATIONS_ICON_LEGEND: state => state.labels.messages.AFFILIATIONS_ICON_LEGEND,
        AFFILIATION_GROUPING_FILTERS: state => state.labels.messages.AFFILIATION_GROUPING_FILTERS,
        AFFILIATION_GROUPING_LEGEND: state => state.labels.messages.AFFILIATION_GROUPING_LEGEND,
        AFFILIATION_STRENGTH_NONE_LEGEND: state => state.labels.messages.AFFILIATION_STRENGTH_NONE_LEGEND,
        AFFILIATION_STRENGTH_0_LEGEND: state => state.labels.messages.AFFILIATION_STRENGTH_0_LEGEND,
        AFFILIATION_STRENGTH_1_LEGEND: state => state.labels.messages.AFFILIATION_STRENGTH_1_LEGEND,
        AFFILIATION_STRENGTH_2_LEGEND: state => state.labels.messages.AFFILIATION_STRENGTH_2_LEGEND,
        ALWAYS_SHOW_KEY_STAKEHOLDERS: state => state.labels.messages.ALWAYS_SHOW_KEY_STAKEHOLDERS,
        CHILDREN_ICON_LEGEND: state => state.labels.messages.CHILDREN_ICON_LEGEND,
        FILTER_APPLY_BUTTON: state => state.labels.messages.FILTER_APPLY_BUTTON,
        FILTER_CLEAR_BUTTON: state => state.labels.messages.FILTER_CLEAR_BUTTON,
        FIND_BUTTON: state => state.labels.messages.FIND_BUTTON,
        HAS_INFLUENCE_LEGEND: state => state.labels.messages.HAS_INFLUENCE_LEGEND,
        NO_INFLUENCE_LEGEND: state => state.labels.messages.NO_INFLUENCE_LEGEND,
        KEY_STAKEHOLDER_LEGEND: state => state.labels.messages.KEY_STAKEHOLDER_LEGEND,
        NAVIGATE_TO_ACCOUNT: state => state.labels.messages.NAVIGATE_TO_ACCOUNT,
        NAVIGATE_TO_TIMELINE: state => state.labels.messages.NAVIGATE_TO_TIMELINE,
        ONLY_SHOW_ACCOUNTS_WITH_AFFILIATIONS: state => state.labels.messages.ONLY_SHOW_ACCOUNTS_WITH_AFFILIATIONS,
        ONLY_SHOW_AFFILIATIONS_WITH_INFLUENCE: state => state.labels.messages.ONLY_SHOW_AFFILIATIONS_WITH_INFLUENCE,
        ROOT_ACCOUNT_LEGEND: state => state.labels.messages.ROOT_ACCOUNT_LEGEND,
        SPECIALTIES_FILTER: state => state.labels.messages.SPECIALTIES_FILTER,
        STAKEHOLDER_NAVIGATOR_PAGE_NAME: state => state.labels.messages.STAKEHOLDER_NAVIGATOR_PAGE_NAME,
        TERRITORIES_FILTER: state => state.labels.messages.TERRITORIES_FILTER,
        TOGGLE_AFFILIATIONS: state => state.labels.messages.TOGGLE_AFFILIATIONS,
        UNFILTERED_PARENT_LEGEND: state => state.labels.messages.UNFILTERED_PARENT_LEGEND,
        ALL: state => state.labels.messages.ALL,
        RECORD_TYPE_LABEL: state => state.labels.messages.RECORD_TYPE_LABEL,
        LOADING_ERROR_GENERIC: state => state.labels.messages.LOADING_ERROR_GENERIC,
        LIMIT_EXCEEDED: state => state.labels.messages.LIMIT_EXCEEDED,
        getAccountTranslation: (state) => (field) => {
            return state.labels.objectTranslations.account[field];
        },
        getAffiliationTranslation: (state) => (field) => {
            return state.labels.objectTranslations.affiliation[field];
        },
        getPicklistTranslation: (state) => (field, value) => {
            return state.labels.picklistTranslations[field] && state.labels.picklistTranslations[field][value] ? state.labels.picklistTranslations[field][value] : value;
        },
        getGraphErrorState: (state) => state.ui.error,
        getGraphErrorMessage: (state) => state.ui.errorMessage,
        getGraphErrorDetail: (state) => state.ui.errorDetail
    },
    mutations: {
        setGraphLoadingState(state, payload) {
            state.ui.loading = payload;
        },
        setGraphErrorState(state, payload) {
            state.ui.error = payload;
        },
        setGraphErrorDetail(state, payload) {
            state.ui.errorDetail = payload;
        },
        setGraphErrorMessage(state, payload) {
            state.ui.errorMessage = payload;
        },
        setVeevaMessages(state, payload) {
            let keys = Object.keys(payload);
            for (let key of keys) {
                if (state.labels.messages[key]) {
                    state.labels.messages[key] = payload[key];
                }
            }
        },
        setObjectTranslations(state, payload) {
            state.labels.objectTranslations.account = payload.account;
            state.labels.objectTranslations.affiliation = payload.affiliation;
        },
        setPicklistTranslations(state, payload) {
            state.labels.picklistTranslations = payload;
        }
    },
    actions: {
        showLoadingModal(context) {
            context.commit("setGraphLoadingState", true);
        },
        hideLoadingModal(context) {
            context.commit("setGraphLoadingState", false);
        },
        openErrorModal(context) {
            context.commit("setGraphErrorState", true);
        },
        closeErrorModal(context) {
            context.commit("setGraphErrorState", false);
        }
    }
});