<template>
  <div class="account-detail">
    <img class="close-icon" src="icons/redCloseIcon.png" @click="$emit('close-detail')"/>
    <img class="record-type-image" :src="resourceUrl"/>
    <h2 class="title two-line" v-bind:title="formattedName">{{ formattedName }}</h2>
    <h4 class="description one-line" v-bind:title="description">{{ description }}</h4>
    <div class="tab-bar">
        <button class="tab-button info-tab" 
                :class="{'tab-selected': showInfo, 'tab-unselected': !showInfo}" 
                @click="clickInfoTab">
            <img class="tab-icon" src="icons/info.png">
        </button>
        <button v-if="!disableAffTab"
                class="tab-button aff-tab" 
                :class="{'tab-selected': showAff, 'tab-unselected': !showAff}" 
                @click="clickAffTab"
                :disabled="disableAffTab">
            <img v-if="!disableAffTab" class="tab-icon" src="icons/affiliationIcon.png">
            <img v-if="disableAffTab" class="tab-icon" src="icons/affiliationDisabledIcon.png">
        </button>
        <button v-if="!disableChildTab"
                class="tab-button child-tab" 
                :class="{'tab-selected': showChild, 'tab-unselected': !showChild}" 
                @click="clickChildTab"
                :disabled="disableChildTab">
            <img v-if="!disableChildTab" class="tab-icon" src="icons/personIcon.png">
            <img v-if="disableChildTab" class="tab-icon" src="icons/personDisabledIcon.png">
        </button>
    </div>
    <div class="tab-content" v-show="showInfo">
        <hr class="spacer-10px" />
        <div class="account-info-container">
            <account-info :account="account"/>
        </div>
        <hr class="spacer-10px" />
    </div>
    <div class="tab-content" v-if="showAff">
        <ul>
            <li v-for="aff in affiliations" :key="aff.To + aff.From">
                <affiliation-tile 
                    :aff="aff" 
                    :parentId="account.Id" 
                    @toggle-accounts="$emit('toggle-accounts', $event)"
                    @ping-account="$emit('ping-account', $event)"/>
            </li>
        </ul>
    </div>
    <div class="tab-content" v-if="showChild">
        <ul>
            <li v-for="child in children" :key="child.Id">
                <child-tile 
                    :account="child"
                    @toggle-accounts="$emit('toggle-accounts', $event)"
                    @ping-account="$emit('ping-account', $event)"/>
            </li>
        </ul>
    </div>
  </div>
</template>

<script>
import AccountInfo from './AccountInfo.vue'
import ChildTile from './ChildTile.vue'
import AffiliationTile from './AffiliationTile.vue'

export default {
    name: 'AccountDetail',
    components: {
        AccountInfo,
        ChildTile,
        AffiliationTile
    },
    data() {
        return {
            showInfo: true,
            showAff: false,
            showChild: false,
            children: [],
            affiliations: []
        }
    },
    props: {
        account: {
            type: Object
        }
    },
    watch: {
        account: {
            immediate: true,
            deep: true,
            handler(newAccount, oldAccount) {
                this.resourceUrl = newAccount.recordTypeResourceUrl;
                this.formattedName = newAccount.Formatted_Name_vod__c;
                this.description = newAccount.Account_Identifier_vod__c;
                this.disableAffTab = newAccount.Affiliations && newAccount.Affiliations.length <= 0;
                this.disableChildTab = newAccount.Children && newAccount.Children.length <= 0;
                if (newAccount && oldAccount && newAccount.Children === oldAccount.Children) {
                    this.children = [];
                    if (newAccount.Children) {
                        newAccount.Children.forEach(child => {
                            this.children.push(Object.assign({}, child));
                        });
                    }
                } else {
                    this.children = newAccount.Children;
                }
                if (newAccount && oldAccount && newAccount.Affiliations === oldAccount.Affiliations) {
                    this.affiliations = [];
                    if (newAccount.Children) {
                        newAccount.Affiliations.forEach(aff => {
                            this.affiliations.push(Object.assign({}, aff));
                        });
                    }
                } else {
                    this.affiliations = newAccount.Affiliations;
                }
                if (this.showChild && newAccount.Children.length <= 0) {
                    this.showChild = false;
                    this.showInfo = true;
                } else if (this.showAff && newAccount.Affiliations.length <= 0) {
                    this.showAff = false;
                    this.showInfo = true;
                }
            }
        }
    },
    methods: {
        clickInfoTab() {
            if (!this.showInfo) {
                this.showInfo = true;
                this.showAff = false;
                this.showChild = false;
            }
        },
        clickAffTab() {
            if (!this.showAff) {
                this.showInfo = false;
                this.showAff = true;
                this.showChild = false;
            }
        },
        clickChildTab() {
            if (!this.showChild) {
                this.showInfo = false;
                this.showAff = false;
                this.showChild = true;
            }
        }
    }
}
</script>

