<template>
    <div class="filter-detail">
        <div class="affiliation-section">
            
            <h3 class="title">{{ACCOUNT_GROUPING_FILTERS}}</h3>
            <div class="filter-item">
                <div class="left-section">
                    <img v-if="showKeyStakeholder" class="checkbox-icon" src="icons/checked.png" @click="toggleShowKeyStakeholder()"/>
                    <img v-else class="checkbox-icon" src="icons/unchecked.png" @click="toggleShowKeyStakeholder()"/>
                </div>
                <div class="right-section">
                    {{ALWAYS_SHOW_KEY_STAKEHOLDERS}}
                </div>
            </div>

            <div>
                <div class="left-section">
                    <img v-if="showAcctWithAff" class="checkbox-icon" src="icons/checked.png" @click="toggleShowAcctWithAff()"/>
                    <img v-else class="checkbox-icon" src="icons/unchecked.png" @click="toggleShowAcctWithAff()"/>
                </div>
                <span class="right-section">    
                    {{ONLY_SHOW_ACCOUNTS_WITH_AFFILIATIONS}}
                </span>
            </div>

            <multiselect-picklist :label="SPECIALTIES_FILTER"
                                :selectedValues="selSpecialities" 
                                :allValues="specialties"
                                v-if="showSpecialties"
                                @submit-multiselect="specialtiesSubmit($event)" />

            <multiselect-picklist :label="TERRITORIES_FILTER"
                                :selectedValues="selTerritories" 
                                :allValues="territories"
                                v-if="showTerritories"
                                @submit-multiselect="territoriesSubmit($event)" />
                
        </div>
        <hr>
        <div class="account-section">
            <h3 class="title">{{AFFILIATION_GROUPING_FILTERS}}</h3>
            <div class="filter-item">
                <div class="left-section">
                    <img v-if="showAffWithInf" class="checkbox-icon" src="icons/checked.png" @click="toggleShowAffWithInf()"/>
                    <img v-else class="checkbox-icon" src="icons/unchecked.png" @click="toggleShowAffWithInf()"/>
                </div>
                <div class="right-section">
                    {{ONLY_SHOW_AFFILIATIONS_WITH_INFLUENCE}}
                </div>
            </div>
            <multiselect-picklist :label="$store.getters.getAffiliationTranslation('Relationship_Strength_vod__c')" 
                                :selectedValues="selStrength" 
                                :allValues="strength"
                                v-if="showStrength"
                                @submit-multiselect="strengthSubmit($event)" />
            
            <multiselect-picklist :label="$store.getters.getAffiliationTranslation('Role_vod__c')"
                                :selectedValues="selRole" 
                                :allValues="role"
                                v-if="showRole"
                                @submit-multiselect="roleSubmit($event)" />

            <multiselect-picklist :label="$store.getters.getAffiliationTranslation('Therapeutic_Area_vod__c')"
                                :selectedValues="selTherapeuticArea" 
                                :allValues="therapeuticArea"
                                v-if="showTherapeuticArea"
                                @submit-multiselect="therapeuticAreaSubmit($event)" />
        </div>
        <div class="button-section">
            <button class="clear-button" @click="clearFilter">{{FILTER_CLEAR_BUTTON}}</button>
            <button class="apply-button" @click="applyFilter">{{FILTER_APPLY_BUTTON}}</button>
        </div>
    </div>
</template>

<script>
import MultiselectPicklist from './MultiselectPicklist.vue'
import { mapGetters } from 'vuex'

export default {
    name: 'FilterMenu',
    components: {
        MultiselectPicklist
    },
    data() {
        return {
            showKeyStakeholder: false,
            showAcctWithAff: false,
            showAffWithInf: false,
            specialties: [],
            territories: [],
            strength: [],
            role: [],
            therapeuticArea: [],
            selSpecialities: [],
            selTerritories: [],
            selStrength: [],
            selRole: [],
            selTherapeuticArea: [],
            filterObj: {
                account: {},
                affiliation: {}
            }
        }
    },
    props: {
        filterValues: {
            type: Object
        }
    },
    computed: {
        showSpecialties() {
            return this.specialties && this.specialties.length > 0;
        },
        showTerritories() {
            return this.territories && this.territories.length > 0;
        },
        showStrength() {
            return this.strength && this.strength.length > 0;
        },
        showRole() {
            return this.role && this.role.length > 0;
        },
        showTherapeuticArea() {
            return this.therapeuticArea && this.therapeuticArea.length > 0;
        },
        ...mapGetters([
            'ACCOUNT_GROUPING_FILTERS',
            'ALWAYS_SHOW_KEY_STAKEHOLDERS',
            'ONLY_SHOW_ACCOUNTS_WITH_AFFILIATIONS',
            'SPECIALTIES_FILTER',
            'TERRITORIES_FILTER',
            'AFFILIATION_GROUPING_FILTERS',
            'ONLY_SHOW_AFFILIATIONS_WITH_INFLUENCE',
            'FILTER_CLEAR_BUTTON',
            'FILTER_APPLY_BUTTON'
        ])
    },
    watch: {
        filterValues: {
            immediate: true,
            deep: true,
            handler(val) {
                if (val && val.account) {
                    this.specialties = this.addTranslations(val.account.specialty);
                    this.territories = this.addTranslations(val.account.territory);
                }
                if (val && val.affiliation) {
                    this.strength = this.addTranslations(val.affiliation.strength);
                    this.role = this.addTranslations(val.affiliation.role);
                    this.therapeuticArea = this.addTranslations(val.affiliation.therapeuticArea);
                }
            }
        }
    },
    methods: {
        addTranslations(filterValues) {
            filterValues.forEach(fv => {
                fv.translation = this.$store.getters.getPicklistTranslation(fv.field, fv.value);
            })
            let sortVals = filterValues.sort((a, b) => {
                let al = a.translation.toLowerCase();
                let bl = b.translation.toLowerCase();
                return (al<bl?-1:(al>bl?1:0));
            });
            return this.removeDuplicates(sortVals);
        },
        removeDuplicates(filterValues) {
            let returnArr = [];
            let valueMap = {};
            filterValues.forEach(val => {
                if (!valueMap[val.value]) {
                    valueMap[val.value] = true;
                    returnArr.push(val);
                } 
            });
            return returnArr;
        },
        specialtiesSubmit(newSelSpecialties) {
            this.selSpecialities = newSelSpecialties;
            this.filterObj.account.specialty = newSelSpecialties;
        },
        territoriesSubmit(newSelTerritories) {
            this.selTerritories = newSelTerritories;
            this.filterObj.account.territory = newSelTerritories;
        },
        strengthSubmit(newSelStrength) {
            this.selStrength = newSelStrength;
            this.filterObj.affiliation.strength = newSelStrength;
        },
        roleSubmit(newSelRole) {
            this.selRole = newSelRole;
            this.filterObj.affiliation.role = newSelRole;
        },
        therapeuticAreaSubmit(newSelTherapeuticArea) {
            this.selTherapeuticArea = newSelTherapeuticArea;
            this.filterObj.affiliation.therapeuticArea = newSelTherapeuticArea;
        },
        toggleShowKeyStakeholder() {
            this.showKeyStakeholder = !this.showKeyStakeholder;
            this.filterObj.account.showKeyStakeholder = this.showKeyStakeholder;
        },
        toggleShowAcctWithAff() {
            this.showAcctWithAff = !this.showAcctWithAff;
            this.filterObj.account.showAcctWithAff = this.showAcctWithAff;
        },
        toggleShowAffWithInf() {
            this.showAffWithInf = !this.showAffWithInf;
            this.filterObj.affiliation.showAffWithInf = this.showAffWithInf;
        },
        clearFilter() {
            this.showKeyStakeholder = false;
            this.showAcctWithAff = false;
            this.showAffWithInf = false;
            this.selSpecialities = [];
            this.selTerritories = [];
            this.selStrength = [];
            this.selRole = [];
            this.selTherapeuticArea = [];
            this.filterObj = {
                account: {},
                affiliation: {}
            }
        },
        applyFilter() {
            this.$emit("apply-filter", this.filterObj);
        },
        getInitObjectCopy(initState) {
            let initStateCopy = {};
            Object.keys(initState).forEach(v => {
                initStateCopy[v] = initState[v];
            });
            return initStateCopy;
        }
    }
}
</script>

