export default class AffiliationFilter {

    constructor() {
        this.strength = [];
        this.role = [];
        this.therapeuticArea = [];
        this.showAffWithInf = false;
    }
    
    
    loadConfig(config) {
        //if config has changed, set new config and return if filtering should be reprocessed
        const newConfig = this.getConfigWithDefaults(config);
        const isEqual = this.isNewConfigEqual(newConfig);
        if (!isEqual) {
            this.strength = newConfig.strength;
            this.role = newConfig.role;
            this.therapeuticArea = newConfig.therapeuticArea;
            this.showAffWithInf = newConfig.showAffWithInf;
        }
        return !isEqual;
    }

    getConfigWithDefaults(config) {
        let configWithDefaults = {}
        if (config.showAffWithInf !== undefined) {
            configWithDefaults.showAffWithInf = config.showAffWithInf;
        } else {
            configWithDefaults.showAffWithInf = false;
        }
        if (config.strength) {
            configWithDefaults.strength = config.strength;
        } else {
            configWithDefaults.strength = [];
        }
        if (config.role) {
            configWithDefaults.role = config.role;
        } else {
            configWithDefaults.role = [];
        }
        if (config.therapeuticArea) {
            configWithDefaults.therapeuticArea = config.therapeuticArea;
        } else {
            configWithDefaults.therapeuticArea = [];
        }
        return configWithDefaults;
    }

    isNewConfigEqual(config) {
        return config.showAffWithInf === this.showAffWithInf &&
               this.compareArray(config.strength, this.strength) &&
               this.compareArray(config.role, this.role) &&
               this.compareArray(config.therapeuticArea, this.therapeuticArea);
    }

    compareArray(arrA, arrB) {
        let isEqual = true;
        if (arrA.length !== arrB.length) {
            //fast return to prevent unecessary processing
            return false;
        }
        for (let i = 0; i < arrA.length; i++) {
            if (arrA.indexOf(arrB[i]) < 0) {
                isEqual = false;
                break;
            }
            if (arrB.indexOf(arrA[i]) < 0) {
                isEqual = false;
                break;
            }
        }
        return isEqual;
    }

    meetsTherapeuticAreaCriteria(aff) {
        let meetsCriteria = false;
        if (this.therapeuticArea.length === 0) {
            meetsCriteria = true;
        } else if (aff.TherapeuticArea === undefined || aff.TherapeuticArea.length === 0) {
            meetsCriteria = false;
        } else {
            for (let t of this.therapeuticArea) {
                if (aff.TherapeuticArea.indexOf(t) > -1) {
                    meetsCriteria = true;
                    break;
                }
            }
        }
        return meetsCriteria;
    }

    meetsCriteria(aff) {
        return (!this.showAffWithInf || aff.Influence === "Has influence"  || aff.Influence === "Is influenced") &&
               (this.strength.length === 0 || this.strength.indexOf(aff.Strength.toString()) > -1) &&
               (this.role.length === 0 || this.role.indexOf(aff.Role) > -1) &&
               this.meetsTherapeuticAreaCriteria(aff);
    }
}