export default class AccountLoader{

    constructor(accountsToLoad, rootHasUnloadedParent, newGraphRoot, initialAccounts) {
        this.loadComplete = false;
        this.batches = this.createAccountBatches(accountsToLoad);
        this.rootHasUnloadedParent = rootHasUnloadedParent;
        this.newGraphRoot = newGraphRoot;
        this.accounts = [];
        this.affiliations = [];
        this.initialAccounts = initialAccounts;
    }

    createAccountBatches(accountsToLoad) {
        let batchSize = 1000;
        let batches = [];
        let numBatches = Math.ceil(accountsToLoad.length / batchSize);
        for (let i = 0; i < numBatches; i++) {
            let accountsInBatch = accountsToLoad.slice(i * batchSize, (i + 1) * batchSize);
            batches.push(this.createBatch(accountsInBatch));
        }
        return batches;
    }

    createBatch(accounts) {
        return {
            batchId: accounts[0],
            accounts: accounts,
            isLoaded: false
        }
    }

    getBatch(batchId) {
        for (let batch of this.batches) {
            if (batch.batchId == batchId) {
                return batch;
            }
        }
        return;
    }

    isLoadComplete() {
        for (let batch of this.batches) {
            if (!batch.isLoaded) {
                return false;
            }
        }
        return true;
    }

    loadBatch(batchResult) {
        let batch = this.getBatch(batchResult.batchId);
        if (batch && !batch.isLoaded) {
            if (batchResult.accounts) {
                this.accounts = this.accounts.concat(batchResult.accounts);
            }
            if (batchResult.affiliations) {
                this.affiliations = this.affiliations.concat(batchResult.affiliations);
            }
            batch.isLoaded = true;
        }
        return this.isLoadComplete();
    }

    getData() {
        return {
            accounts: this.accounts,
            affiliations: this.affiliations,
            rootHasUnloadedParent: this.rootHasUnloadedParent,
            newGraphRoot: this.newGraphRoot,
            initialAccounts: this.initialAccounts
        };
    }

    getBatches() {
        return this.batches;
    }
}