<template>
    <div class="graph-menu">
        <div>
            <button class="menu-item" :class="{'close-icon': showSearch}" @click="toggleSearch">
                <img v-if="showSearch" src="icons/close.png" />
                <img v-else src="icons/search.png" />
            </button>
        </div>
        <div>
            <button class="menu-item" :class="{'close-icon': showFilter}" @click="toggleFilter">
                <img v-if="showFilter" src="icons/close.png" />
                <img v-else src="icons/filter.png" />
            </button>
        </div>
        <div>
            <button class="menu-item" :class="{'close-icon': showLegend}" @click="toggleLegend">
                <img v-if="showLegend" src="icons/close.png" />
                <img v-else src="icons/legend.png" />
            </button>
        </div>
        <div>
            <button class="menu-item" @click="$emit('fit')"> 
                <img src="icons/fit.png" />
            </button>
        </div>
        <div>
            <button class="menu-item" @click="$emit('zoom-in')"> 
                <img src="icons/plus.png" />
            </button>
        </div>
        <div>
            <button class="menu-item" @click="$emit('zoom-out')"> 
                <img src="icons/minus.png" />
            </button>
        </div>

        <div class="modal-container" v-if="showSearch">
            <div class="left-arrow arrow-search" />
            <search :results="searchResult" 
                    @toggle-accounts="$emit('toggle-accounts', $event)"
                    @ping-account="$emit('ping-account', $event)"
                    @search="$emit('search', $event)" />
        </div>

        <div class="modal-container" v-show="showFilter">
            <div class="left-arrow arrow-filter" />
            <filter-menu :filterValues="filterValues" @apply-filter="applyFilter($event)"/>
        </div>

        <div class="modal-container" v-show="showLegend">
            <div class="left-arrow arrow-legend" />
            <graph-legend />
        </div>
    </div>
</template>

<script>
import Search from './Search.vue'
import FilterMenu from './FilterMenu.vue'
import GraphLegend from './GraphLegend.vue'
            
export default {
    name: 'GraphMenu',
    components: {
        Search,
        FilterMenu,
        GraphLegend
    },
    props: {
        searchResult: {
            type: Array
        },
        filterValues: {
            type: Object
        }
    },
    data() {
        return {
            showSearch: false,
            showFilter: false,
            showLegend: false
        }
    },
    watch: {
        showSearch: function(val) {
            this.clearSearchValues(val);
        }
    },
    methods: {
        toggleSearch() {
            this.showSearch = !this.showSearch;
            if (this.showSearch) {
                this.showFilter = false;
                this.showLegend = false;
            }
        },
        toggleFilter() {
            this.showFilter = !this.showFilter;
            if (this.showFilter) {
                this.showSearch = false;
                this.showLegend = false;
            }
        },
        toggleLegend() {
            this.showLegend = !this.showLegend;
            if (this.showLegend) {
                this.showSearch = false;
                this.showFilter = false;
            }
        },
        applyFilter(filterObj) {
            this.showFilter = false;
            this.$emit('apply-filter', filterObj);
        },
        clearSearchValues(isCloseSearchMenu) {
            if (!isCloseSearchMenu) {
                this.$emit('clear-search');
            }
        }
    }
}
</script>

