export default class Chart {
    
    constructor() {
        this.cachedNodes = {};
        this.cachedAffs = [];
        this.expandSet = {};
        this.collapseSet = {};
        this.c;
    }

    //public
    init(accountTimelineClick, accountDetailClick, timelineLabel, detailLabel) {
        this.initGraph(accountTimelineClick, accountDetailClick, timelineLabel, detailLabel);
    }

    //public
    updateAffiliationDisplay(showAff) {
        if (showAff) {
            this.c.config.clinks = this.cachedAffs;
        } else {
            this.c.config.clinks = [];
        }
    }

    //public
    updateGraphNodes(rootNode) {
        this.expandArr = [];
        this.collapseArr = [];
        this.processNodes(rootNode);
        this.c.expandCollapse("", this.expandArr, this.collapseArr);
    }

    //public
    updateSingleNode(nodeModel) {
        let processedNode = this.createNode(nodeModel);
        this.c.update(processedNode);
    }

    //public
    redrawChart(shouldFit, centerId) {
        if (centerId && this.c.get(centerId) !== null) {
            this.c.center(centerId);
        } else {
            this.c.draw();
            if (shouldFit) {
                this.c.fit();
            }
        } 
    }
    
    //public
    removeLoadMoreNode(id) {
        let loadMoreNodeId = "loadmore-" + id;
        this.removeNode(loadMoreNodeId);
    }

    //public
    removeLoadParentNode(id) {
        let loadParentNodeId = "loadparent-" + id;
        this.removeNode(loadParentNodeId);
    }

    //public
    setRedrawHandler(handler) {
        this.c.on('redraw', handler);
    }

    //public
    setNodeOnClickHandler(handler) {
        this.c.on('click', handler);
    }

    //public
    expandCollapseNodes(parentId, expandNodeIds, collapseNodeIds) {
        this.c.expandCollapse(parentId, expandNodeIds, collapseNodeIds);
    }

    //public
    loadAffiliations(affs) {
        this.addAffiliations(affs);
    }

    //public
    getNodeInfo(id) {
        return this.c.getNode(id);
    }

    //public
    ping(id) {
        this.c.ripple(id);
    }

    //public
    center(id) {
        this.c.center(id);
        this.c.ripple(id);
    }

    //public
    search(searchString) {
        return this.c.search(searchString, ["name", "title"], ["id"]);
    }

    //public
    fit() {
       this.c.fit();
    }

    //public
    zoomIn() {
        this.c.zoom(true);
     }

    //public
    zoomOut() {
        this.c.zoom(false);
     }

    initGraph(accountTimelineClick, accountDetailClick, timelineLabel, detailLabel) {
        this.c = new window.OrgChart(document.getElementById("tree"), {
            template: "baseNode",
            layout: window.OrgChart.tree,
            enableSearch: false,
            nodeBinding: {
                img_0: "img",
                field_0: "name",
                field_1: "title",
                sortVal: "sortVal",
                numChild: "numChild",
                numChildId: "numChildId",
                numAff: "numAff"
            },
            toolbar: {
                fullScreen: false,
                zoom: false,
                fit: false,
                expandAll: false
            },
            nodeMenu: this.createBaseNodeMenu(accountTimelineClick, accountDetailClick, timelineLabel, detailLabel),
            zoom: {
                speed: 200,
                smooth: 4
            },
            tags: {
                "loading": {
                    template: "loadingNode"
                },
                "unloadedChildrenAffOnly": {
                    template: "unloadedChildrenAffOnly"
                },
                "unloadedChildrenNoAff": {
                    template: "unloadedChildrenNoAff"
                },
                "expandCollapseBoth": {
                    template: "expandCollapseBoth"
                },
                "expandCollapseChildOnly": {
                  template: "expandCollapseChildOnly"
                },
                "loadParent": {
                    template: "loadParentNode"
                },
                "loadMore": {
                    template: "loadMoreNode"
                },
                "affOnlyBase": {
                    template: "affOnlyBase"
                }
            },
            nodes: [
                this.createLoadingNode("root")
            ],
            orderBy: "sortVal"
        });
    }

    processNodes(root) {
        root.Children.forEach(node => {
            this.processNodes(node);
        });
        let processedNode = this.createNode(root);
        const existsInChart = this.c.get(processedNode.id) !== null;
        const action = this.getNodeAction(processedNode, root.isFiltered, existsInChart);
        this.setExpandCollapseStates(root);
        if (action !== "ignore") {         
            this.addOrRemoveSupportingNodes(root);
            if (action === "add") {
                this.c.add(processedNode);
            } else if (action == "update") {
                this.c.update(processedNode);
            } else {
                this.c.remove(processedNode.id);
            }
            this.cachedNodes[processedNode.id] = processedNode;
        }   
    }

    getNodeAction(newNode, isFiltered, existsInChart) {
        let action = "";
        const cachedNode = this.cachedNodes[newNode.id];
        //cases:
        //add node - node not in graph, not filtered
        //update node - node in graph, not filtered, and new node is different from cached node
        //remove node - node in graph, filtered
        //ignore node - not not in graph, filtered or node is the same

        if (!existsInChart && !isFiltered) {
            action = "add";
        } else if (existsInChart && isFiltered) {
            action = "remove";
        } else if (existsInChart && !isFiltered && !this.nodeEqualityCheck(newNode, cachedNode)) {
            action = "update";
        } else {
            action = "ignore";
        }
        return action;
    }

    setExpandCollapseStates(root) {
        if (!root.isFiltered && root.Children.length > 0) {
            root.Children.forEach(child => {
                if (!child.isCollapsed && !root.isChildrenCollapsed && child.isVisible) {
                    this.expandArr.push(child.Id);
                } else {
                    this.collapseArr.push(child.Id);
                }
            });
        }
    }

    addOrRemoveSupportingNodes(nodeModel) {     
        //if has hidden children, add the load more node
        const loadMoreId = "loadmore-" + nodeModel.Id;
        const loadMoreNodeExists = this.c.get(loadMoreId) !== null;

        if (nodeModel.hasHiddenChildren && !loadMoreNodeExists) {
            const loadMoreNode = this.createLoadMoreNode(nodeModel.Id);
            this.c.add(loadMoreNode);
        } else if (!nodeModel.hasHiddenChildren && loadMoreNodeExists) {
            this.c.remove(loadMoreId);
        }

        const loadParentId = "loadparent-" + nodeModel.Id;
        const loadParentNodeExists = this.c.get(loadParentId) !== null;

        if (nodeModel.hasUnloadedParent && !loadParentNodeExists) {
            const loadParentNode = this.createLoadParentNode(nodeModel.Id);
            this.c.add(loadParentNode);
        } else if (!nodeModel.hasUnloadedParent && loadParentNodeExists) {
            this.c.remove(loadParentId);
        }
    }

    addAffiliations(affs) {
        let processedAffiliations = [];
        affs.forEach(affDataModel => {
            const aff = this.createAffiliationClink(affDataModel);
            processedAffiliations.push(aff);
        });
        this.cachedAffs = processedAffiliations;
    }
    
    createAffiliationClink(aff) {
        const template = this.getClinkTemplate(aff);
        let to = aff.To;
        let from = aff.From;
        if (aff.Influence === "Is influenced") {
            to = aff.From;
            from = aff.To;
        }
        return {
            to: to,
            from: from,
            template: template
        };
    }

    getClinkTemplate(aff) {
        if (aff.Influence === "Has influence" || aff.Influence === "Is influenced") {
            return "inf" + this.parseStrength(aff.Strength);
        } else {
            return "noInf" + this.parseStrength(aff.Strength);
        }
    }

    parseStrength(strength) {
        let s = "";
        if (strength === "0" || strength === "1" || strength === "2") {
            s = strength;
        }
        return s;
    }

    createNode(node) {
        return {
            id: node.Id,
            name: node.Formatted_Name_vod__c,
            title: node.Account_Identifier_vod__c,
            email: "test@veeva.com",
            img: node.recordTypeResourceUrl,
            pid: node.ParentId,
            sortVal: node.sortVal,
            tags: this.getNodeTags(node),
            numChild: node.numberChildren,
            numAff: node.Affiliations.length,
            numChildId: node.ChildrenIds.length
        };
    }


    getNodeTags(node) {
        let tags = [];
        tags.push(this.getNodeTemplate(node));
        if (node.numberChildren > 0) {
            if (node.isChildrenCollapsed) {
                tags.push("collapsed");
            } else {
                tags.push("expanded");
            }
        }
        if (!node.meetsFilterCriteria && !node.isInitialRoot) {
            tags.push("filterException");
        }
        if (node.isInitialRoot) {
            tags.push("initialRoot");
        }
        if (node.isKeyStakeholder) {
            tags.push("keyStakeholder");
        }
        if (node.isSelected) {
            tags.push("selected");
        }
        return tags;
    }

    getNodeTemplate(node) {
        let template = "baseNode";
        const hasUnloadedChildren = node.ChildrenIds.length > 0 && node.Children.length === 0 && !node.childrenLoaded;
        const numberAff = node.Affiliations.length;
        if (hasUnloadedChildren && numberAff > 0) {
            template = "unloadedChildrenAffOnly";
        } else if (hasUnloadedChildren && numberAff === 0) {
            template = "unloadedChildrenNoAff";
        } else if (node.numberChildren > 0 && numberAff > 0) {
            template = "expandCollapseBoth";
        } else if (node.numberChildren > 0 && numberAff === 0) {
            template = "expandCollapseChildOnly";
        } else if (numberAff > 0) {
            template = "affOnlyBase";
        }
        return template;
    }
    
    createLoadingNode(id) {
        let loadingNode = {
            id: "loading-" + id,
            name: "",
            title: "",
            img: "",
            pid: id,
            tags: ["loading"],
            sortVal: 0
          };
          return loadingNode;
    }

    createLoadMoreNode(id) {
        let loadMoreNode = {
            id: "loadmore-" + id,
            name: "Load More",
            title: "",
            img: "",
            pid: id,
            tags: ["loadMore"],
            sortVal: 999999
          };
          return loadMoreNode;
    }

    createLoadParentNode(id) {
        let loadMoreNode = {
            id: "loadparent-" + id,
            name: "Load Parent",
            title: "",
            img: "",
            pid: "",
            tags: ["loadParent"],
            sortVal: 999999
          };
          return loadMoreNode;
    }

    removeNode(id) {
        if (this.c && this.c.get(id)) {
            this.c.remove(id);
        }
    }

    nodeEqualityCheck(a, b) {
        const tagsEqual = this.tagEqualityCheck(a.tags, b.tags);
        return tagsEqual &&
                a.name === b.name &&
                a.title === b.title &&
                a.img === b.img &&
                a.pid === b.pid &&
                a.numChild === b.numChild &&
                a.numAff === b.numAff;
    }

    tagEqualityCheck(a, b) {
        if (a.length !== b.length) {
            return false;
        }
        //tags are guaranteed to be in the same order
        for (let i = 0; i < a.length; i++) {
            let atag = a[i];
            let btag = b[i];
            if (atag !== btag) {
                return false;
            }
        }
        return true;
    }

    createBaseNodeMenu(accountTimelineClick, accountDetailClick, timelineLabel, detailLabel) {
        return {
            toTimeline: {
                text: timelineLabel,
                icon: "",
                onClick: accountTimelineClick
            },
            toAccountDetail: {
                text: detailLabel,
                icon: "",
                onClick: accountDetailClick
            }
        }
    }
}