import AffiliationFilter from './affiliationFilter.js'
import AccountFilter from './accountFilter.js'

export default class DataModel {

    constructor() {
        this.allNodes = [];
        this.currentValidAffs = [];
        this.affiliationMap = {};
        this.childParentMap = {};
        this.nodeMap = {};
        this.recordTypes = {};
        this.keyStakeholders = [];
        this.showAff = false;
        this.chartRootId = "";
        this.initialRootId = "";
        this.EXPAND_LIMIT = 10;
        this.affFilter = new AffiliationFilter();
        this.nodeFilter = new AccountFilter();
        this.filterValues = this.initializeFilterValues();
        this.accountCount = 0;
        this.hierarchy = {};
        this.nodeToParentMap = {};
    }

    //public
    init(keyStakeholders, rootId) {
        this.keyStakeholders = keyStakeholders;
        this.chartRootId = rootId;
        this.initialRootId = rootId;
    }
    
    //public
    showAffiliations() {
        return this.showAff;
    }

    //public
    addData(d) {
        //this.removeUnloadedChildrenFlag(d.rootId);
        this.addDataToModel(d);
    }

    //public
    getAffiliations() {
        return this.currentValidAffs;
    }

    //public
    toggleAffiliations() {
        if (this.showAff) {
            this.showAff = false;
        } else {
            this.showAff = true;
        }
    }

    //public
    getTree() {
        return this.nodeMap[this.chartRootId];
    }

    //public
    expandNodeChildren(id) {
        //set visibility for newly expanded children
        //children are expanded in chunks of EXPAND_LIMIT
        //return list of node Ids that were expanded
        let idsToShow = [];
        let node = this.nodeMap[id];
        let showCounter = this.EXPAND_LIMIT;
        let hasHiddenChildren = false;
        for (let child of node.Children) {
            if (child.isCollapsed && !child.isPinned && !node.isFiltered) {
                if (showCounter > 0) {
                    child.isCollapsed = false;
                    child.isPinned = true;
                    showCounter--;
                    idsToShow.push(child.Id);
                } else {
                    hasHiddenChildren = true;
                    break;
                }
            }
        }
        node.hasHiddenChildren = hasHiddenChildren;
        return idsToShow;
    }

    //public
    getNode(id) {
        return this.nodeMap[id];
    }

    //public
    setAffiliationFilter(config) {
        const hasChanged = this.affFilter.loadConfig(config);
        if (hasChanged) {
            this.populateNodeAffiliations();
        }
        return hasChanged
    }

    //public
    setAccountFilter(config) {
        return this.nodeFilter.loadConfig(config);
    }

    //public
    filterAccounts() {
        this.cleanNodeState();
        this.applyAccountFilter();
        this.postFilterProcessing(this.nodeMap[this.chartRootId]);
    }

    //public
    getFilterValues() {
        return {
            affiliation: {
                strength: Object.values(this.filterValues.affiliation.strength),
                role: Object.values(this.filterValues.affiliation.role),
                therapeuticArea: Object.values(this.filterValues.affiliation.therapeuticArea)
            },
            account: {
                territory: Object.values(this.filterValues.account.territory),
                specialty: Object.values(this.filterValues.account.specialty)
            }
        }
    }

    //public
    addHierarchy(hierarchyToAdd) {
        this.populateNodeToParentMap(hierarchyToAdd);
        Object.assign(this.hierarchy, hierarchyToAdd);
    }
    
    //public
    getChildrenIdsFromHierarchy(id) {
        let childrenIds = this.hierarchy[id];
        return childrenIds && childrenIds.length > 0 ? childrenIds : [];
    }

    //public
    getParentIdFromHierarchy(id) {
        const allParents = Object.keys(this.hierarchy);
        for (let parent of allParents) {
            const childrenOfParent = this.hierarchy[parent];
            if (childrenOfParent && childrenOfParent.indexOf(id) >= 0) {
                return parent;
            }
        }
    }

    initializeFilterValues() {
        return {
            affiliation: {
                strength: {},
                role: {},
                therapeuticArea: {}
            },
            account: {
                territory: {},
                specialty: {}
            }
        }
    }

    addDataToModel(d) {
        //get raw nodes processed
        let processedNodes = this.processNodes(d.accounts);
        //mark nodes as loaded
        this.markNodesLoaded(d.initialAccounts);
        if (d.newGraphRoot) {
            let oldRootNode = this.nodeMap[this.chartRootId];
            this.updateChartRootNode(oldRootNode, d.newGraphRoot);
            processedNodes.push(oldRootNode);
        }
        if (d.rootHasUnloadedParent) {
            let node = this.nodeMap[this.chartRootId];
            node.ParentId = "loadparent-" + this.chartRootId;
            node.hasUnloadedParent = true;
        }
        //process raw affiliations and add affiliations to nodes
        this.processAffiliations(d.affiliations);
        //create tree model
        this.buildTreeModel(processedNodes, this.chartRootId);
        //apply filter
        this.filterAccounts();
        this.setChildrenVisibility(this.nodeMap[this.chartRootId]);
    }

    markNodesLoaded(accountsToLoad) {
        for (let accId of accountsToLoad) {
            let node = this.getNode(accId);
            if (node) {
                node.childrenLoaded = true;
            }
        }
    }

    updateChartRootNode(oldRoot, newRootId) {
        oldRoot.hasUnloadedParent = false;
        oldRoot.ParentId = newRootId;
        oldRoot.sortVal = -1;
        this.chartRootId = newRootId;      
    }

    processNodes(rawNodes) {
        let processedNodes = [];
        rawNodes.forEach(rawNode => {
            let processedNode = this.processNode(rawNode);
            if (processedNode) {
                processedNodes.push(processedNode);
            }
        });
        return processedNodes;
    }

    processNode(rawNode) {
        //takes rawNode data from Apex queries and inserts it into the data model
        const id = rawNode.account.Id;
        //fast fail for case node that is already in the graph
        //this happens when there are children with multiple parents
        if (this.childParentMap[id]) {
            return;
        }

        let processedNode = this.createNode(rawNode);
        this.populateAccountFilterValues(processedNode);
        
        this.allNodes.push(processedNode);
        this.nodeMap[id] = processedNode;
        this.childParentMap[id] = processedNode.ParentId;

        return processedNode;
    }

    populateAccountFilterValues(node) {
        const territories = node.Territories;
        const spec1 = node.Specialty_1_vod__c;
        const spec2 = node.Specialty_2_vod__c;
        const gSpec1 = node.Group_Specialty_1_vod__c;
        const gSpec2 = node.Group_Specialty_2_vod__c;
        if (territories && territories.length > 0) {
            territories.forEach(territory => {
                const terr = this.createFilterValue(territory, "territory");
                this.filterValues.account.territory[terr.key] = terr;
            });
        }
        if (spec1) {
            let spec1Obj = this.createFilterValue(spec1, "Specialty_1_vod__c");
            this.filterValues.account.specialty[spec1Obj.key] = spec1Obj;
        }       
        if (spec2) {
            let spec2Obj = this.createFilterValue(spec2, "Specialty_2_vod__c");
            this.filterValues.account.specialty[spec2Obj.key] = spec2Obj;
        }
        if (gSpec1) {
            let gSpec1Obj = this.createFilterValue(gSpec1, "Group_Specialty_1_vod__c");
            this.filterValues.account.specialty[gSpec1Obj.key] = gSpec1Obj;
        }
        if (gSpec2) {
            let gSpec2Obj = this.createFilterValue(gSpec2, "Group_Specialty_1_vod__c");
            this.filterValues.account.specialty[gSpec2Obj.key] = gSpec2Obj;
        }
    }

    populateAffiliationFilterValues(aff) {
        const strength = aff.Strength;
        const role = aff.Role;
        const therapeuticArea = aff.TherapeuticArea;

        if (strength !== undefined && strength !== "") {
            const strengthObj = this.createFilterValue(strength, "Relationship_Strength_vod__c");
            this.filterValues.affiliation.strength[strengthObj.key] = strengthObj;
        }

        if (role && role !== "") {
            let roleObj = this.createFilterValue(role, "Role_vod__c");
            this.filterValues.affiliation.role[roleObj.key] = roleObj;
        }

        if (therapeuticArea && therapeuticArea.length > 0) {
            therapeuticArea.forEach(ta => {
                const therapeuticAreaObj = this.createFilterValue(ta, "Therapeutic_Area_vod__c")
                this.filterValues.affiliation.therapeuticArea[therapeuticAreaObj.key] = therapeuticAreaObj;
            });
        }
    }

    createFilterValue(value, field) {
        return {
            value: value,
            field: field,
            key: value + "-" + field
        }
    }

    createNode(rawNode) {
        let node = rawNode.account;
        node.isSelected = false;
        node.sortVal = 0;
        node.isKeyStakeholder = this.keyStakeholders.indexOf(node.Id) > -1;
        node.isInitialRoot = (node.Id === this.initialRootId);
        node.recordTypeResourceUrl = this.getRecordTypeResourceUrl(node.RecordType.DeveloperName, node.RecordType.IsPersonType);
        node.ParentId = this.nodeToParentMap[node.Id];
        node.Territories = rawNode.territories;
        
        node.hasUnloadedParent = false;
        node.ChildrenIds = this.hierarchy[node.Id] ? this.hierarchy[node.Id] : [];
        node.Children = [];
        node.Affiliations = [];
        node.numberChildren = 0;
        node.childrenLoaded = false; 

        //tracking visibility
        node.isVisible = false;
        node.isCollapsed = !node.Id === this.chartRootId;
        node.isChildrenCollapsed = false;
        node.hasHiddenChildren = false;
        node.isPinned = node.Id === this.chartRootId;

        //for filtering
        //distinction made between meeting criteria and isFiltered,
        //as node can be still shown on the graph while not meeting the criteria
        node.meetsFilterCriteria = false;
        node.isFiltered = true;  
        
        this.accountCount++;
        return node;
    }

    processAffiliations(affiliations) {
        //create affiliation data model objects and adds it to affiliation map
        affiliations.forEach(aff => {
            this.createAffiliation(aff);
        });
        //adds affiliations to related nodes and popualates currentValidAffs
        this.populateNodeAffiliations();
    }

    createAffiliation(aff) {
        const from = aff.From_Account_vod__c;
        const to = aff.To_Account_vod__c;
        const affId = to + "-" + from;
        const inverseAffId = from + "-" + to;
        if (!this.affiliationMap[affId] && !this.affiliationMap[inverseAffId]) {
            let processedAff = {
                To: to,
                From: from,
                Strength: aff.Relationship_Strength_vod__c ? aff.Relationship_Strength_vod__c : "",
                Influence: aff.Influence_vod__c,
                TherapeuticArea: aff.Therapeutic_Area_vod__c ? aff.Therapeutic_Area_vod__c.split(';') : [],
                Role: aff.Role_vod__c ? aff.Role_vod__c : "",
                Comments: aff.Comments_vod__c ? aff.Comments_vod__c : "",
                Id: aff.Id
            }
            this.affiliationMap[affId] = processedAff;
        }
    }

    populateNodeAffiliations() {
        //filters the affiliations
        //and populates the node's affiliation data
        this.clearNodeAffiliationData();
        this.currentValidAffs = [];
        const allAffiliationIds = Object.keys(this.affiliationMap);
        allAffiliationIds.forEach(id => {
            let aff = this.affiliationMap[id];
            let fromNode = this.nodeMap[aff.From];
            let toNode = this.nodeMap[aff.To];
            if (fromNode && toNode) {
                this.populateAffiliationFilterValues(aff);
                if (this.affFilter.meetsCriteria(aff)) {
                    aff.ToAcct = toNode;
                    aff.FromAcct = fromNode;
                    this.currentValidAffs.push(aff);
                    fromNode.Affiliations.push(aff);
                    toNode.Affiliations.push(aff);
                }
            }
        })
    }

    parseStrengthToInt(strength) {
        let strengthInt = parseInt(strength);
        if (isNaN(strengthInt) || strengthInt < 0) {
            strengthInt = 0;
        } else if (strengthInt > 3) {
            strengthInt = 3;
        }
        return strengthInt;
    }

    clearNodeAffiliationData() {
        const allNodeIds = Object.keys(this.nodeMap);
        allNodeIds.forEach(id => {
            this.nodeMap[id].Affiliations = [];
        });
    }

    removeUnloadedChildrenFlag(rootId) {
        let node = this.nodeMap[rootId];
        if (node) {
            node.hasUnloadedChildren = false;
        }
    }

    buildTreeModel(nodes, localRootId) {
        nodes.forEach(node => {
            const isRoot = localRootId === node.Id;
            if (!isRoot) {
                let parent = this.nodeMap[node.ParentId];
                if (parent) {
                    parent.Children.push(node);
                }
            }
        });
    }

    postFilterProcessing(rootNode) {
        //sort and set node states post filtering
        //set isVisible - any parent is not visible
        //set isCollapsed - is paginated and not yet toggled
        //set hasHiddenChildren - any children are not visible that haven't been manually toggled off

        let children = rootNode.Children;
        if (children.length > 0) {
            if (!rootNode.childrenSorted) {
                children.sort(this.sortChildrenComparator);
                rootNode.childrenSorted = true;
            }

            let visibleChildrenCounter = this.EXPAND_LIMIT;
            let nonFilteredChildrenCount = 0;
            let hasHiddenChildren = false;

            for (let i = 0; i < children.length; i++) {
                let child = children[i];
                child.sortVal = i;

                if (!child.isFiltered) {
                    nonFilteredChildrenCount++;
                }

                if (!rootNode.isVisible || rootNode.isChildrenCollapsed) {
                    child.isVisible = false;
                }

                if (visibleChildrenCounter === 0 && child.isCollapsed && !child.isPinned && !hasHiddenChildren) {
                    hasHiddenChildren = true;
                }

                if (visibleChildrenCounter > 0 && !child.isFiltered && !(child.isPinned && child.isCollapsed)) {
                    visibleChildrenCounter--;
                    child.isCollapsed = false;
                }

                this.postFilterProcessing(child);
            }
            rootNode.hasHiddenChildren = hasHiddenChildren;
            rootNode.numberChildren = nonFilteredChildrenCount;
        }
        return;
    }

    setChildrenVisibility(root) {
        root.Children.forEach(child => {
            child.isVisible = !root.isChildrenCollapsed && !root.isCollapsed && root.isVisible;
            this.setChildrenVisibility(child);
        });
    }

    sortChildrenComparator(a, b) {
        //First check for sortVal < 0.  If sortVal is -1, then node is intentionally meant to be first in sort list
        if (a.sortVal < b.sortVal) {
            return -1;
        } else if (a.sortVal > b.sortVal) {
            return 1;
        } else {
            //if not pre-sorted, first sort by person vs non-person account
            if (!a.RecordType.IsPersonType && b.RecordType.IsPersonType) {
                return -1;
            } else if (a.RecordType.IsPersonType && !b.RecordType.IsPersonType) {
                return 1;
            } else {
                //then name
                let aName = a.Formatted_Name_vod__c.toUpperCase();
                let bName = b.Formatted_Name_vod__c.toUpperCase();
                if (aName < bName) {
                    return -1;
                } else if (aName > bName) {
                    return 1;
                } else {
                    return 0;
                }
            }
        }
    }

    cleanNodeState() {
        //set node default state to collapsed and hidden
        //filtering explicity sets these states if the node should be shown
        this.allNodes.forEach(node => {
            //ignore reset if node is pinned, otherwise set to true unless node is root node
            if (!node.isPinned) {
                node.isCollapsed = node.Id !== this.chartRootId;
            }
            //reset isCollapsed to false unless node is chart root
            node.isVisible = true;
            node.meetsFilterCriteria = false;
            node.isFiltered = true;
            node.hasHiddenChildren = false;
        });
    }

    applyAccountFilter() {
        this.allNodes.forEach(node => {
            //ignore filter if node has unloaded children, or it is the inital root of the graph
            const ignoreFilter = (node.ChildrenIds.length > 0 && node.Children.length == 0 && !node.childrenLoaded) || node.isInitialRoot;
            const meetsCriteria = this.nodeFilter.meetsCriteria(node);
            node.meetsFilterCriteria = meetsCriteria;
            if (meetsCriteria || ignoreFilter) {
                node.isFiltered = false;
                this.markParentsAsNotFiltered(node);
            }
        });
    }

    markParentsAsNotFiltered(node) {
        let parent = this.nodeMap[node.ParentId];
        if (parent && parent.isFiltered) {
            parent.isFiltered = false;
            this.markParentsAsNotFiltered(parent);
        }
    }

    getRecordTypeResourceUrl(devName, isPerson) {
        let resourceName = "";
        switch(devName) {
            case "Business_Professional_vod":
                resourceName = "business_professional";
                break;
            case "Professional_vod":
                resourceName = "professional";
                break;
            case "MCOPlan_vod":
            case "MCO_vod":
                resourceName = "mco";
                break;
            case "Practice_vod":
                resourceName = "private_practice";
                break;
            case "Hospital_vod":
                resourceName = "hospital";
                break;
            case "Pharmacy_vod":
                resourceName = "pharmacy";
                break;
            case "HospitalDepartment_vod":
                resourceName = "hospital_department";
                break;
            case "Laboratory_vod":
                resourceName = "laboratory";
                break;
            case "Organization_vod":
            case "Board_vod":
                resourceName = "board";
                break;
            case "Employer_vod":
                resourceName = "employer";
                break;
            case "ExtendedCare_vod":
                resourceName = "ambulatory";
                break;
            case "Institution_vod":
                resourceName = "institution";
                break;
            case "Publication_vod":
                resourceName = "publication";
                break;
            case "KOL_vod":
                resourceName = "kol";
                break;
            case "Wholesaler_vod":
                resourceName = "wholesaler";
                break;
            case "Government_Agency_vod":
                resourceName = "government";
                break;
            case "Distributor_vod":
            case "Distributor_Branch_vod":
                resourceName = "distributor";
                break;
            case "PersonAccount":
                resourceName = "person";
                break;
            default:
                if (isPerson) {
                    resourceName = "person";
                } else {
                    resourceName = "business"
                }
        }
        return "icons/svg/" + resourceName + ".svg";
    }

    getAccountCount() {
        return this.accountCount;
    }

    populateNodeToParentMap(hierarchy) {
        Object.keys(hierarchy).forEach(parent => {
            const children = hierarchy[parent];
            children.forEach(child => {
                this.nodeToParentMap[child] = parent;
            })
        });
    }
}