export default class AccountFilter{
    constructor() {
        this.showAcctWithAff = false;
        this.showKeyStakeholder = false;
        this.specialty = [];
        this.territory = [];
    }

    loadConfig(config) {
        const newConfig = this.getConfigWithDefaults(config);
        const isEqual = this.isNewConfigEqual(newConfig);
        if (!isEqual) {
            this.showAcctWithAff = newConfig.showAcctWithAff;
            this.showKeyStakeholder = newConfig.showKeyStakeholder;
            this.specialty = newConfig.specialty;
            this.territory = newConfig.territory;
        }
        return !isEqual;
    }

    isNewConfigEqual(config) {
        const isEqual = config.showAcctWithAff === this.showAcctWithAff &&
               config.showKeyStakeholder === this.showKeyStakeholder &&
               this.compareArray(config.specialty, this.specialty) && 
               this.compareArray(config.territory, this.territory);
        return isEqual;
    }

    getConfigWithDefaults(config) {
        let configWithDefaults = {};
        if (config.showAcctWithAff !== undefined) {
            configWithDefaults.showAcctWithAff = config.showAcctWithAff;
        } else {
            configWithDefaults.showAcctWithAff = false;
        }

        if (config.showKeyStakeholder !== undefined) {
            configWithDefaults.showKeyStakeholder = config.showKeyStakeholder;
        } else {
            configWithDefaults.showKeyStakeholder = false;
        }
        
        if (config.specialty && config.specialty.length > 0) {
            configWithDefaults.specialty = config.specialty;
        } else {
            configWithDefaults.specialty = [];
        }

        if (config.territory && config.territory.length > 0) {
            configWithDefaults.territory = config.territory;
        } else {
            configWithDefaults.territory = [];
        }
        return configWithDefaults;
    }

    compareArray(arrA, arrB) {
        let isEqual = true;
        if (arrA.length !== arrB.length) {
            //fast return to prevent unecessary processing
            return false;
        }
        for (let i = 0; i < arrA.length; i++) {
            if (arrA.indexOf(arrB[i]) < 0) {
                isEqual = false;
                break;
            }
            if (arrB.indexOf(arrA[i]) < 0) {
                isEqual = false;
                break;
            }
        }
        return isEqual;
    }

    meetsCriteria(node) {
        //key stakeholder is an OR relationship with the other filter criteria
        return  (this.showKeyStakeholder && node.isKeyStakeholder) || 
                (
                    (!this.showAcctWithAff || node.Affiliations.length > 0) &&
                    this.meetsSpecialtyCriteria(node) &&
                    this.meetsTerritoryCriteria(node)
                );
    }

    meetsSpecialtyCriteria(node) {
        return this.specialty.length === 0 || 
               (node.Specialty_1_vod__c !== undefined && this.specialty.indexOf(node.Specialty_1_vod__c) > -1) ||
               (node.Specialty_2_vod__c !== undefined && this.specialty.indexOf(node.Specialty_2_vod__c) > -1) ||
               (node.Group_Specialty_1_vod__c !== undefined && this.specialty.indexOf(node.Group_Specialty_1_vod__c) > -1) ||
               (node.Group_Specialty_2_vod__c !== undefined && this.specialty.indexOf(node.Group_Specialty_2_vod__c) > -1);
    }

    meetsTerritoryCriteria(node) {
        let meetsTerritoryCriteria = false;
        if (this.territory.length === 0) {
            meetsTerritoryCriteria = true;
        } else if (node.Territories === undefined || node.Territories.length === 0) {
            meetsTerritoryCriteria = false;
        } else {
            for (let t of this.territory) {
                if (node.Territories.indexOf(t) > -1) {
                    meetsTerritoryCriteria = true;
                    break;
                }
            }
        }
        return meetsTerritoryCriteria;
    }
}