import Chart from './chart.js'
import DataModel from './dataModel.js'
import MessagingService from './messagingService.js'
import AccountLoader from "@/core/util/AccountLoader.js";

export default class GraphController {

    constructor() {
        this.chart = new Chart();
        this.dataModel = new DataModel();
        this.ms = new MessagingService();
        this.selected = "none";
        this.accountLoader;
    }

    init(timelineClick, detailClick, redrawHandler, nodeClick, detailLabel, timelineLabel, keyStakeholders, graphRootId) {
        this.dataModel.init(keyStakeholders, graphRootId);
        this.chart.init(timelineClick, detailClick, timelineLabel, detailLabel);
        this.chart.setRedrawHandler(redrawHandler);
        this.chart.setNodeOnClickHandler(nodeClick);
    }

    loadHierarchy(data, isInitialLoad) {
        this.dataModel.addHierarchy(data.hierarchy);
        let accountsToLoad = [];
        if (isInitialLoad) {
            accountsToLoad = this.getAccountsToLoadInitial(data.rootAccounts, data.hierarchy, data.depth);
        } else if (data.newGraphRoot) {
            accountsToLoad = this.getAccountsToLoadUp(data.newGraphRoot, this.dataModel.getTree().Id, data.hierarchy);
        } else {
            accountsToLoad = data.rootAccounts;
        }
        let finalAccountsToLoad = this.removeExistingNodes(accountsToLoad);
        const rootAccounts = this.getLoadedAccounts(data.rootAccounts, isInitialLoad, data.depth, data.newGraphRoot);

        if (finalAccountsToLoad.length === 0) {
            //if all accounts to load are already in the graph, then we have a cyclical hierarchy
            //the state of these nodes need to be updated to prevent more load requests
            this.cancelLoadEvent(data, rootAccounts);
        }
      
        this.accountLoader = new AccountLoader(finalAccountsToLoad, data.rootHasUnloadedParent, data.newGraphRoot, rootAccounts);
        return this.accountLoader.getBatches();
    }

    getLoadedAccounts(rootAccounts, isInitialLoad, depth, newGraphRoot) {
        //determine the accounts who should have children loaded
        let loadedAccounts = [];
        
        if (newGraphRoot) {
            loadedAccounts.push(newGraphRoot);
        } else if (rootAccounts && rootAccounts.length > 0) {
            if (isInitialLoad) {
                if (depth === 2) {
                    loadedAccounts = rootAccounts.concat(this.dataModel.getChildrenIdsFromHierarchy(rootAccounts[0]));
                } else {
                    loadedAccounts = rootAccounts;
                }
            } else {
                const parentId = this.dataModel.getParentIdFromHierarchy(rootAccounts[0]);
                if (parentId) {
                    loadedAccounts.push(parentId);
                }
            }
        }
        
        return loadedAccounts;
    }

    processLoadBatch(data) {
        const isComplete = this.accountLoader.loadBatch(data);
        if (isComplete) {
            this.addData(this.accountLoader.getData());
        }
        return isComplete;
    }

    addData(data) {
        this.dataModel.addData(data);
        this.updateNodes(data.rootId);
    }

    updateNodes(centerId) {
        this.chart.updateGraphNodes(this.dataModel.getTree());
        this.chart.loadAffiliations(this.dataModel.getAffiliations());
        this.chart.updateAffiliationDisplay(this.dataModel.showAffiliations());
        this.chart.redrawChart(false, centerId);
    }

    toggleAffiliations() {
        this.dataModel.toggleAffiliations();
        this.chart.updateAffiliationDisplay(this.dataModel.showAffiliations());
        this.chart.redrawChart(false);
    }

    removeExistingNodes(accounts) {
        let nonLoadedAccounts = [];
        accounts.forEach(account => {
            if (!this.dataModel.getNode(account)) {
                nonLoadedAccounts.push(account);
            }
        });
        return nonLoadedAccounts;
    }

    cancelLoadEvent(data, rootAccounts) {
        if (data.newGraphRoot) {
            //if cancelling the load parent event, remove the load parent node
            //for the current graph root
            let graphRoot = this.dataModel.getTree();
            graphRoot.hasUnloadedParent = false;
            this.chart.removeLoadParentNode(this.dataModel.getTree().Id);
        } else {
            //if cancelling a load children event, simply mark the accounts as loaded
            rootAccounts.forEach(accountId => {
                let node = this.dataModel.getNode(accountId);
                node.childrenLoaded = true;
            });
        }
    }

    getAccountsToLoadInitial(rootAccounts, hierarchy, depth) {
        let firstLevel = [];       
        rootAccounts.forEach(account => {
            const childrenOfAccount = hierarchy[account];
            if (childrenOfAccount && childrenOfAccount.length > 0) {
                firstLevel = firstLevel.concat(childrenOfAccount);
            }
        });
 
        let secondLevel = [];
        if (depth === 2) {
            firstLevel.forEach(account => {
                const childrenOfAccount = hierarchy[account];
                if (childrenOfAccount && childrenOfAccount.length > 0) {
                    secondLevel = secondLevel.concat(childrenOfAccount);
                }
            });
        }
        return rootAccounts.concat(firstLevel, secondLevel);
    }

    getAccountsToLoadUp(newRootId, oldRootId, hierarchy) {
        let accountsToLoad = [newRootId];
        const newRootChildren = hierarchy[newRootId];
        if (newRootChildren) {
            newRootChildren.forEach(child => {
                if (child !== oldRootId) {
                    accountsToLoad.push(child);
                }
            });
        }
        return accountsToLoad;
    }

    expandCollapseNodes(id) {
        let node = this.dataModel.getNode(id);
        const isExpand = node.isChildrenCollapsed;
        const visibleIds = this.getUncollapsedNodes(node);
        if (isExpand) {
            node.isChildrenCollapsed = false;
            this.dataModel.setChildrenVisibility(node);
            this.chart.expandCollapseNodes("", visibleIds, []);
            
        } else {
            node.isChildrenCollapsed = true;
            this.dataModel.setChildrenVisibility(node);
            this.chart.expandCollapseNodes("", [], visibleIds);
            
        }
        this.chart.updateSingleNode(node);
        this.chart.redrawChart(false);
    }

    getUncollapsedNodes(node) {
        let visibleIds = [];
        if (node.hasHiddenChildren) {
            visibleIds.push("loadmore-" + node.Id);
        }
        node.Children.forEach(child => {
            if (!child.isCollapsed) {
                visibleIds.push(child.Id);
            }
        });
        return visibleIds;
    }

    toggleAccountVisibility(id) {
        let node = this.dataModel.getNode(id);
        if (!node || this.dataModel.chartRootId === id) {
            return;
        }
        node.isPinned = true;
        const isShow = node.isCollapsed || !node.isVisible;
        if (isShow) {
            if (node.ParentId && node.ParentId !== "root") {
                let parentNode = this.dataModel.getNode(node.ParentId);
                this.expandChain(parentNode);
            }
            if (node.isCollapsed) {
                this.expandSingleNode(node);
            }
            this.chart.ping(id);
        } else {
            this.chart.expandCollapseNodes("", [], [id]);
            node.isCollapsed = true;
            this.dataModel.setChildrenVisibility(node);
        }
    }

    expandSingleNode(node) {
        let visibleIds = [];
        if (!node.isChildrenCollapsed && node.Children.length > 0) {
            visibleIds.push(...this.getUncollapsedNodes(node));
        }
        visibleIds.push(node.Id);
        this.chart.expandCollapseNodes("", visibleIds, node.Children.map(child => child.Id));
        node.isCollapsed = false;
        this.dataModel.setChildrenVisibility(node);
    }

    expandChain(rootNode) {  
        if (rootNode && (rootNode.isCollapsed || !rootNode.isVisible || rootNode.isChildrenCollapsed)) {
            if (rootNode.ParentId && rootNode.ParentId !== "root") {
                let parentNode = this.dataModel.getNode(rootNode.ParentId);
                this.expandChain(parentNode);
            }
            if (rootNode.isCollapsed) {
                this.expandSingleNode(rootNode);
            }
            if (rootNode.isChildrenCollapsed) {
                const visibleIds = this.getUncollapsedNodes(rootNode);
                rootNode.isChildrenCollapsed = false;
                this.chart.updateSingleNode(rootNode);
                this.dataModel.setChildrenVisibility(rootNode);
                this.chart.expandCollapseNodes("", visibleIds, []);
            }
        }
    }

    nodeClickHandler(sender, args) {
        let action = "detailView";
        const nodeId = args.node.id;
        const nodeIdSplits = nodeId.split('-');
        if (nodeIdSplits[0] === "loadmore") {
            this.chart.ping(nodeId);
            let node = this.dataModel.getNode(nodeIdSplits[1]);
            if (node) {
                this.expandSiblings(node.Id, node.ParentId);
            }
            action = "loadMore";
        } else if (nodeIdSplits[0] === "loadparent") {
            action = "loadParent";
        }
        return action;
    }

    processFilterRequest(filterObject) {
            const shouldProcessAff = this.dataModel.setAffiliationFilter(filterObject.affiliation);
            const shouldProcessAcc = this.dataModel.setAccountFilter(filterObject.account);
            
            if (shouldProcessAff) {
                this.chart.loadAffiliations(this.dataModel.getAffiliations());
            }
            if (shouldProcessAcc || shouldProcessAff) {
                this.dataModel.filterAccounts();
                this.chart.updateGraphNodes(this.dataModel.getTree());
                this.chart.updateAffiliationDisplay(this.dataModel.showAffiliations());
                this.chart.redrawChart(false);
            }
    }

    expandSiblings(id) {
        const nodesToExpand = this.dataModel.expandNodeChildren(id);
        let node = this.dataModel.getNode(id);
        if (!node.hasHiddenChildren) {
            //remove load more node if node no longer has hidden children
            this.chart.removeLoadMoreNode(id);
        }
        nodesToExpand.forEach(nodeId => {
            let node = this.dataModel.getNode(nodeId);
            this.expandSingleNode(node);
        });
    }

    accountTimelineClick(nodeId) {
        this.ms.sendRedirectRequest("timeline", nodeId);
    }

    accountDetailClick(nodeId) {
        this.ms.sendRedirectRequest("detail", nodeId);
    }

    getNodeDataModel(nodeId) {
        return this.dataModel.getNode(nodeId);
    }

    fit() {
        this.chart.fit();
    }

    zoomIn() {
        this.chart.zoomIn();
    }

    zoomOut() {
        this.chart.zoomOut();
    }

    updateNodeSelected(id, isSelected) {
        let node = this.dataModel.getNode(id);
        node.isSelected = isSelected;
        this.chart.updateSingleNode(node);
    }

    selectAccount(id) {
        if (this.selected !== "none") {
            this.updateNodeSelected(this.selected, false);
        }
        this.selected = id;
        this.updateNodeSelected(this.selected, true);
        this.chart.redrawChart(false);
    }

    deselectAccount() {
        if (this.selected !== "none") {
            this.updateNodeSelected(this.selected, false);
            this.chart.redrawChart(false);
        }
    }

    pingAccount(id) {
        let node = this.dataModel.getNode(id);
        if (node.isCollapsed || !node.isVisible) {
            this.toggleAccountVisibility(id);
        }
        this.chart.center(id);
    }

    getSearchResult(searchString) {
        if (!searchString || searchString === "") {
            return [];
        }
        const matchingNodes = this.chart.search(searchString);
        let finalResult = [];
        matchingNodes.forEach(node => {
            if (node && node.id) {
                let nodeDataModel = this.dataModel.getNode(node.id);
                if (nodeDataModel) {
                    finalResult.push(nodeDataModel);
                }
            }
        });
        return finalResult;
    }

    getFilterValues() {
        return this.dataModel.getFilterValues();
    }

    getAccountCount() {
        return this.dataModel.getAccountCount();
    }

    getNodesToLoad(id) {
        return this.dataModel.getNode(id).ChildrenIds;
    }
}