<template>
    <div class="multiselect-section">
        <div class="multiselect-info" @click="toggleShowSelection">
            <div class="label-section">
                {{label}}
            </div>
            <div class="value-section">
                {{valueText}}
            </div>
        </div>
        <div v-show="showSelection" >
            <hr>
            <div class="picklist-detail">
                <ul>
                    <li v-for="v in allValues" :key="v.key">
                        <span class="checkbox-container">
                            <img v-show="valueState[v.value]" class="checkbox-icon" src="icons/checked.png" @click="toggleValue(v)"/>
                            <img v-show="!valueState[v.value]" class="checkbox-icon" src="icons/unchecked.png" @click="toggleValue(v)"/>
                        </span>
                        <span class="value-container">
                            {{v.translation}}
                        </span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
    
</template>

<script>

export default {
    name: 'MultiselectPicklist',
    data() {
        return {
           valueState: {},
           valueMap: {},
           valueText: this.$store.getters.ALL,
           showSelection: false
        }
    },
    props: {
        label: {
            type: String
        },
        selectedValues: {
            type: Array
        },
        allValues: {
            type: Array
        }
    },
    watch: {
        selectedValues: {
            immediate: true,
            deep: true,
            handler(selValues) {
                
                //resetValueState
                this.valueState = {};
                if (this.allValues.length > 0) {
                    this.allValues.forEach(v => {
                        this.valueState[v.value] = false;
                    });
                }
                //set selected values from prop
                if (selValues.length > 0) {
                    selValues.forEach(v => {
                        this.valueState[v] = true;
                    });
                }
                //reset the displayed text
                this.valueText = this.getValueText();
            }
        },
        allValues: {
            immediate: true,
            deep: true,
            handler(newValues) {    
                //resetValueState
                this.valueState = {};
                this.valueMap = {};
                newValues.forEach(v => {
                    this.valueMap[v.value] = v;
                    this.valueState[v.value] = false;
                });
                //set selected values from prop
                if (this.selectedValues.length > 0) {
                    this.selectedValues.forEach(v => {
                        this.valueState[v] = true;
                    });
                }         
            }
        }
    },
    methods: {
        toggleShowSelection() {
            this.showSelection = !this.showSelection;
        },
        toggleValue(v) {
            let val = this.valueState[v.value];
            if (val) {
                this.valueState[v.value] = !val;
            } else {
                this.valueState[v.value] = true;
            }
            let selValues = [];
            Object.keys(this.valueState).forEach(value => {
                if (this.valueState[value]) {
                    selValues.push(value);
                }
            });
            this.$emit("submit-multiselect", selValues);
        },
        getValueText() {
            let valueText = "";
            if (this.selectedValues.length > 0) {
                valueText = this.selectedValues.map(v => this.valueMap[v].translation).join(', ');
            } else {
                valueText = this.$store.getters.ALL;
            }
            return valueText;
        }
    }
}
</script>

