<template>
  <div class="error-modal">
    <img class="close-icon" src="icons/redCloseIcon.png" @click="closeErrorModal"/>
    <h3 class="generic-error">
        {{getGraphErrorMessage}}
    </h3>
    <div v-if="showDetailSection" class="detail-section" @click="toggleErrorDetail">
        <img class="carrot-icon" :class="{'flipped': showDetail}" src="icons/downCarrot.png"/>
        <p v-if="showDetail">
            {{getGraphErrorDetail}}
        </p>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
    name: 'ErrorModal',
    data () {
        return {
            showDetail: false
        }
    },
    computed: {
        showDetailSection() {
            return this.getGraphErrorDetail !== "";
        },
        ...mapGetters([
            'getGraphErrorMessage',
            'getGraphErrorDetail'
        ])
    },
    methods: {
        closeErrorModal() {
            this.$store.dispatch("closeErrorModal");
        },
        toggleErrorDetail() {
            this.showDetail = !this.showDetail;
        }
    }
}
</script>

