<template>
    <div class="tile-container" :class="affTileClasses" @click="toggleDetailVisibility">
        <table>
            <tr class="top-row">
                <td class="record-type-column" rowspan="3">
                    <img :src="relatedAcct.recordTypeResourceUrl"/>
                </td>
                <td class="name-column one-line" v-bind:title="relatedAcct.Formatted_Name_vod__c">
                    {{relatedAcct.Formatted_Name_vod__c}}
                </td>
                <td rowspan="3">
                    <img v-if="parentAcct.RecordType.IsPersonType" src="icons/personHalf.png" />
                    <img v-else src="icons/nonPersonHalf.png" />
                </td>
                <td v-if="!relatedAcct.isFiltered" class="visibility-column" rowspan="3">
                    <button v-if="isVisible" class="action-button" @click="toggleAccountsVisibility">
                        <img class="visibility-image" src="icons/visible.png" />
                    </button>
                    <button v-if="!isVisible" class="action-button" @click="toggleAccountsVisibility">
                        <img class="visibility-image" src="icons/notVisible.png" />
                    </button>
                    <button class="action-button" @click="pingAccount">
                        <img class="center-image" src="icons/center.png" />
                    </button>
                </td>
            </tr>
            <tr class="middle-row">
                <td class="aff-column">
                    <div :class="[lineColor, lineStyle]">
                        <svg v-if="affDirection === 'right'" height="20px" width="210px">
                            <defs>
                                <marker v-bind:id="aff.Id" markerWidth="10" markerHeight="10px" refX="0" refY="5" orient="auto">
                                    <polygon points="0 2.5, 5 5, 0 7.5" />
                                </marker>
                            </defs>
                            <line x1="0" y1="10" x2="190" y2="10" stroke="#000" stroke-width="4" v-bind:marker-end="markerString" />
                        </svg>
                        <svg v-if="affDirection === 'left'" height="20px" width="210px">
                            <defs>
                                <marker v-bind:id="aff.Id" markerWidth="10" markerHeight="10px" refX="0" refY="5" orient="180deg">
                                    <polygon points="0 2.5, 5 5, 0 7.5" />
                                </marker>
                            </defs>
                            <line x1="20" y1="10" x2="210" y2="10" stroke="#000" stroke-width="4" v-bind:marker-start="markerString" />
                        </svg>
                        <svg v-if="affDirection === 'none'" height="20px" width="210px">
                            <line x1="0" y1="10" x2="210" y2="10" stroke="#000" stroke-width="4" />
                        </svg>
                    </div>
                </td>
            </tr>
            <tr class="bottom-row">
                <td class="info-column one-line" v-bind:title="aff.Role">
                    {{aff.Role}}
                </td>
            </tr>
        </table>
        <div v-if="showDetail">
            <hr class="detail-border" />
            <div class="detail-container">
                <affiliation-info :aff="aff"/>
            </div>
            
        </div>
    </div>
</template>

<script>
import AffiliationInfo from './AffiliationInfo.vue'

export default {
    name: 'AffiliationTile',
    components: {
        AffiliationInfo
    },
    props: {
        aff: {
            type: Object
        },
        parentId: {
            type: String
        }
    },
    data() {
        return {
            showDetail: false
        }
    },
    computed: {
        affTileClasses: function () {
            const classObject = {};
            classObject['not-visible'] = !this.isVisible;
            classObject['filtered'] = this.relatedAcct.isFiltered;
            classObject['initial-root'] = this.relatedAcct.isInitialRoot;
            classObject['key-stakeholder'] = this.relatedAcct.isKeyStakeholder;
            return classObject;
        },
        relatedAcct: function () {
            //one of the affiliation's To or From is always the parent id, choose the opposite
            return this.parentId === this.aff.To ? this.aff.FromAcct : this.aff.ToAcct;
        },
        parentAcct: function () {
            return this.parentId !== this.aff.To ? this.aff.FromAcct : this.aff.ToAcct;
        },
        affDirection: function() {
            let direction = "none";
            if ((this.aff.Influence === "Has influence" && this.aff.To === this.parentId) || 
                (this.aff.Influence === "Is influenced" && this.aff.From === this.parentId)) {
                direction = "right";
            } else if ((this.aff.Influence === "Is influenced" && this.aff.To === this.parentId) || 
                       (this.aff.Influence === "Has influence" && this.aff.From === this.parentId)) {     
                direction = "left";
            }
            return direction;
        },
        lineStyle: function() {
            return (this.aff.Influence && this.aff.Influence !== "No influence") ? "solid" : "striped";
        },
        lineColor: function() {
            let color = "grey";
            if (this.aff.Strength === "0") {
                return "red";
            } else if (this.aff.Strength === "1") {
                return "green";
            } else if (this.aff.Strength === "2") {
                return "blue";
            }
            return color;
        },
        isVisible: function() {
            return this.relatedAcct.isVisible && !this.relatedAcct.isCollapsed;
        },
        markerString: function() {
            return "url(#" + this.aff.Id + ")";
        }
    },
    methods: {
        toggleDetailVisibility() {
            this.showDetail = !this.showDetail;
        },
        toggleAccountsVisibility() {
            this.$emit('toggle-accounts', this.relatedAcct.Id);
            event.cancelBubble = true;
            if(event.stopPropagation) event.stopPropagation();        
        },
        pingAccount() {
            this.$emit('ping-account', this.relatedAcct.Id);
            event.cancelBubble = true;
            if(event.stopPropagation) event.stopPropagation();  
        }
    }
}
</script>

