export default class MessagingService {
    
    sendInitialLoadRequest() {
        this.sendMessage({command: "initGraph"});
    }

    sendInitialHierarchyRequest(id) {
        this.sendMessage({command: "loadInitialHierarchy", rootId: id});
    }

    sendHierarchyRequest(ids) {
        this.sendMessage({command: "loadHierarchy", ids: ids});
    }

    sendParentHierarchyRequest(id) {
        this.sendMessage({command: "loadParentHierarchy", childId: id});
    }

    sendAccountLoadRequest(batchId, accounts) {
        this.sendMessage({command: "loadAccountBatch", batchId: batchId, accounts: accounts});
    }

    sendAccountCount(numberAccounts) {
        this.sendMessage({command: "updateNumberAccounts", numberAccounts: numberAccounts});
    }

    sendPing() {
        this.sendMessage({command: "ping"});
    }

    sendRedirectRequest(page, id) {
        this.sendMessage({command: "redirect", page: page, id: id});
    }

    addMessageHandler(handler) {
        if (typeof window.LCC !== "undefined" && typeof window.LCC.onlineSupport !== "undefined") {
            window.LCC.onlineSupport.addMessageHandler(handler);
        }
        else {
            console.log("Failed To Send Message");
        }
    }

    sendMessage(userMessage) {
        if (typeof window.LCC !== "undefined" && typeof window.LCC.onlineSupport !== "undefined") {
            window.LCC.onlineSupport.sendMessage("containerUserMessage", {payload: userMessage});
        }
        else {
            console.log("Failed To Send Message");
        }
    }
}