<template>
    <div class="info-content">
        <table>
            <tr class="column-row" v-if="showRecordType">
                <td class="column-label">{{$store.getters.RECORD_TYPE_LABEL}}</td>
                <td class="column-value">{{account.RecordType.Name}}</td>
            </tr>
            <tr class="column-row" v-if="showTerritories">
                <td class="column-label">{{$store.getters.TERRITORIES_FILTER}}</td>
                <td class="column-value">{{account.Territories.join(', ')}}</td>
            </tr>
            <tr class="column-row" v-if="showEmail">
                <td class="column-label">{{$store.getters.getAccountTranslation('PersonEmail')}}</td>
                <td class="column-value">{{account.PersonEmail}}</td>
            </tr>
            <tr class="column-row" v-if="showPhone">
                <td class="column-label">{{$store.getters.getAccountTranslation('Phone')}}</td>
                <td class="column-value">{{account.Phone}}</td>
            </tr>
            <tr class="column-row" v-if="showSpecialty1">
                <td class="column-label">{{$store.getters.getAccountTranslation('Specialty_1_vod__c')}}</td>
                <td class="column-value">{{$store.getters.getPicklistTranslation('Specialty_1_vod__c', account.Specialty_1_vod__c)}}</td>
            </tr>
            <tr class="column-row" v-if="showSpecialty2">
                <td class="column-label">{{$store.getters.getAccountTranslation('Specialty_2_vod__c')}}</td>
                <td class="column-value">{{$store.getters.getPicklistTranslation('Specialty_2_vod__c', account.Specialty_2_vod__c)}}</td>
            </tr>
            <tr class="column-row" v-if="showGroupSpecialty1">
                <td class="column-label">{{$store.getters.getAccountTranslation('Group_Specialty_1_vod__c')}}</td>
                <td class="column-value">{{$store.getters.getPicklistTranslation('Group_Specialty_1_vod__c', account.Group_Specialty_1_vod__c)}}</td>
            </tr>
            <tr class="column-row" v-if="showGroupSpecialty2">
                <td class="column-label">{{$store.getters.getAccountTranslation('Group_Specialty_2_vod__c')}}</td>
                <td class="column-value">{{$store.getters.getPicklistTranslation('Group_Specialty_2_vod__c', account.Group_Specialty_2_vod__c)}}</td>
            </tr>
        </table>
    </div>
</template>

<script>
export default {
    name: 'AccountInfo',
    props: {
        account: {
            type: Object
        }
    },
    computed: {
        showEmail: function() {
            return this.account && this.account.PersonEmail;
        },
        showPhone: function() {
            return this.account && this.account.Phone;
        },
        showRecordType: function() {
            return this.account && this.account.RecordType && this.account.RecordType.Name;
        },
        showSpecialty1: function() {
            return this.account && this.account.Specialty_1_vod__c;
        },
        showSpecialty2: function() {
            return this.account && this.account.Specialty_2_vod__c;
        },
        showGroupSpecialty1: function() {
            return this.account && this.account.Group_Specialty_1_vod__c;
        },
        showGroupSpecialty2: function() {
            return this.account && this.account.Group_Specialty_2_vod__c;
        },
        showTerritories: function() {
            return this.account && this.account.Territories && this.account.Territories.length > 0;
        }
    }
}
</script>

