<template>
    <div v-if="!account.isFiltered" class="tile-container" 
        :class="childTileCssClasses" 
        @click="toggleDetailVisibility">
        <table>
            <tr class="top-row">
                <td class="record-type-column" rowspan="3">
                    <img :src="account.recordTypeResourceUrl"/>
                </td>
                <td class="name-column">
                    {{account.Formatted_Name_vod__c}}
                </td>
                <td class="visibility-column" rowspan="3">
                    <button v-if="isVisible" class="action-button" @click="toggleAccountVisibility">
                        <img class="visibility-image" src="icons/visible.png" />
                    </button>
                    <button v-if="!isVisible" class="action-button" @click="toggleAccountVisibility">
                        <img class="visibility-image" src="icons/notVisible.png" />
                    </button>
                    <button class="action-button" @click="pingAccount">
                        <img class="center-image" src="icons/center.png" />
                    </button>
                </td>
            </tr>
            <tr class="middle-row">
                <td class="identifier-column">
                    {{account.Account_Identifier_vod__c}}
                </td>
            </tr>
            <tr class="bottom-row">
                <td class="count-column">
                    <span v-if="account.Children.length > 0">
                        {{account.Children.length}}
                        <img class="small-icon" src="icons/personIcon.png"/>
                    </span>
                    <span v-if="account.ChildrenIds.length > 0 && account.Children.length === 0">
                        {{account.ChildrenIds.length}}
                        <img class="small-icon" src="icons/personIcon.png"/>
                    </span>
                    <span v-if="account.Affiliations.length > 0">
                        {{account.Affiliations.length}}
                        <img class="small-icon" src="icons/affiliationIcon.png"/>
                    </span>
                </td>
            </tr>
        </table>
        <div v-if="showDetail">
            <hr class="detail-border" />
            <div class="detail-container">
                <account-info :account="account"/>
            </div>
        </div>
    </div>
</template>

<script>
import AccountInfo from './AccountInfo.vue'

export default {
    name: 'ChildTile',
    components: {
        AccountInfo
    },
    props: {
        account: Object
    },
    data() {
        return {
            showDetail: false
        }
    },
    computed: {
        isVisible: function() {
            return this.account.isVisible && !this.account.isCollapsed;
        },
        childTileCssClasses() {
        const classObject = {};
        classObject['not-visible'] = !this.isVisible;
        classObject['key-stakeholder'] = this.account.isKeyStakeholder;
        classObject['initial-root'] = this.account.isInitialRoot;
        classObject['unmet-criteria'] = !this.account.meetsFilterCriteria;
        return classObject;
  }

    },
    methods: {
        toggleDetailVisibility() {
            this.showDetail = !this.showDetail;
        },
        toggleAccountVisibility() {
            this.$emit('toggle-accounts', this.account.Id);
            event.cancelBubble = true;
            if(event.stopPropagation) event.stopPropagation();   
        },
        pingAccount() {
            this.$emit('ping-account', this.account.Id);
            event.cancelBubble = true;
            if(event.stopPropagation) event.stopPropagation();  
        }
    }
}
</script>

